import { connect } from 'react-redux';
import I from 'components/I';
import React from 'react';

import { Instruction, Section } from 'modals/styles/Styles';
import {
  RESETTING_PASSWORD,
  updateAccount,
  updatePassword,
  updateRememberMe
} from 'redux/actions/DexAccount';
import { encrypt } from 'modals/components/utils';
import { formatter, utils } from 'lightcone/common';
import { loginModal, resetPasswordModal } from 'redux/actions/ModalManager';
import { notifyError, notifySuccess } from 'redux/actions/Notification';
import { saveUpdateRecord } from 'lightcone/api/localStorgeAPI';
import ConfirmPasswordModal from './ConfirmPasswordModal';
import WhyIcon from 'components/WhyIcon';

import config from 'lightcone/config';

class ResetPasswordModal extends ConfirmPasswordModal {
  title = (<I s="Reset Password" />);
  buttonLabel = (<I s="Reset Password" />);

  instructions = (
    <Section>
      <Instruction>
        <I s="ResetPasswordInstruction_1" />
      </Instruction>
      <ul>
        <li>
          <I s="ResetPasswordInstruction_Timing" />
          <WhyIcon text="TimingWhy" />
        </li>
        <li>
          <I s="ResetPasswordInstruction_Fee" />
          <WhyIcon text="FeeWhy" />
        </li>
      </ul>
    </Section>
  );

  processChange = (address, dexAccount, newAccount) => {
    saveUpdateRecord({
      address: window.wallet.address,
      from: {
        publicKeyX: dexAccount.account.publicKeyX,
        publicKeyY: dexAccount.account.publicKeyY
      },
      to: {
        publicKeyX: newAccount.publicKeyX,
        publicKeyY: newAccount.publicKeyY
      }
    });

    this.props.updateAccount(newAccount);
  };

  onProceed = () => {
    this.setState({
      loading: true
    });
    if (this.state.password.length > 0 && this.state.isPasswordValid) {
      const {
        updateRememberMe,
        dexAccount,
        exchange,
        nonce,
        gasPrice
      } = this.props;
      const { password } = this.state;
      updateRememberMe(true);

      (async () => {
        try {
          const passwordHash = utils.keccakHash(password);
          const fee = formatter
            .toBig(config.getFeeByType('update', exchange.onchainFees).fee)
            .plus(config.getFeeByType('deposit', exchange.onchainFees).fee);
          const createOrUpdateAccountResponse = await window.wallet.createOrUpdateAccount(
            password,
            {
              exchangeAddress: exchange.exchangeAddress,
              fee: fee.toString(),
              chainId: exchange.chainId,
              token: config.getTokenBySymbol('ETH', exchange.tokens),
              amount: '',
              permission: '',
              nonce: nonce.nonce,
              gasPrice: gasPrice.gasPrice
            },
            true
          );

          // const hash = createOrUpdateAccountResponse['data'];
          const keyPair = createOrUpdateAccountResponse['keyPair'];

          if (keyPair.secretKey === undefined) {
            throw new Error(
              'Failed to parse the response of account creation.'
            );
          }
          const keyPairCipherText = encrypt(keyPair.secretKey, passwordHash);

          const account = {
            ...dexAccount.account,
            publicKeyX: keyPair.publicKeyX,
            publicKeyY: keyPair.publicKeyY,
            keyPairCipherText,
            state: RESETTING_PASSWORD,
            password: passwordHash
          };

          this.processChange(window.wallet.address, dexAccount, account);
          notifySuccess(<I s="PasswordResetNotification" />);
        } catch (err) {
          notifyError(<I s="PasswordResetFailedNotofication" />);
        } finally {
          // Reset password
          this.setState({
            isPasswordValid: true,
            password: '',
            repeatedPassword: '',
            passwordMismatch: false
          });

          this.onClose();
        }
      })();
    }
  };
}

const mapStateToProps = state => {
  const { modalManager, dexAccount, nonce, gasPrice, exchange } = state;
  const isVisible = modalManager.isResetPasswordModalVisible;
  return { isVisible, modalManager, dexAccount, nonce, gasPrice, exchange };
};

const mapDispatchToProps = dispatch => {
  return {
    showLoginModal: () => dispatch(loginModal(true)),
    closeModal: () => dispatch(resetPasswordModal(false)),
    updateAccount: account => dispatch(updateAccount(account)),
    updatePassword: password => dispatch(updatePassword(password)),
    updateRememberMe: rem => dispatch(updateRememberMe(rem))
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ResetPasswordModal);
