export const CONNECT_TO_META_MASK = 'CONNECT_TO_META_MASK';
export const UPDATE_META_MASK = 'UPDATE_META_MASK';
export const DETECT_IF_META_MASK_INSTALLED = 'DETECT_IF_META_MASK_INSTALLED';

export const INCREASE_REFERENCE_COUNT = 'INCREASE_REFERENCE_COUNT';

export function increaseReferenceCount() {
  return {
    type: INCREASE_REFERENCE_COUNT,
    payload: {}
  };
}

export function connectToMetaMask(startConnecting) {
  return {
    type: CONNECT_TO_META_MASK,
    payload: {
      startConnecting
    }
  };
}

export function updateMetaMask(payload) {
  return {
    type: UPDATE_META_MASK,
    payload: payload
  };
}

export function detectIfMetaMaskInstalled(installed) {
  return {
    type: DETECT_IF_META_MASK_INSTALLED,
    payload: {
      installed
    }
  };
}
