import {
  CHANGE_ACCOUNT,
  GET_DATA_FROM_LOCALSTORAGE,
  LOGOUT,
  NOT_REGISTERED,
  REGISTERED,
  RESET_PASSWORD,
  UNDEFINED,
  UPDATE_ACCOUNT,
  UPDATE_REMEMBER_ME,
  WALLET_UNCONNECTED,
  getAllAccountStates
} from 'redux/actions/DexAccount';

import {
  getAccountFromLocal,
  removeAccountFromLocal,
  saveAccountToLocal,
  saveLoginRecord
} from 'lightcone/api/localStorgeAPI';

/**
 * account :
 * {
 * address
 * apiKey,
 * accountId
 * publicX,
 * publicY,
 * keyPairCipherText
 * }
 * */
const initialState = {
  account: { state: UNDEFINED },
  rememberMe: true
};

export const DexAccountReducer = (state = initialState, action) => {
  const { payload } = action;
  switch (action.type) {
    case RESET_PASSWORD:
      return {
        ...state,
        password: payload.password
      };
    case UPDATE_REMEMBER_ME:
      return {
        ...state,
        rememberMe: payload.rememberMe
      };
    /**
     * stateaccount
     * **/
    case UPDATE_ACCOUNT:
      const latestAccount = {
        ...state.account,
        ...payload.account
      };
      if (state.rememberMe) {
        saveAccountToLocal(latestAccount);
      }
      return {
        ...state,
        account: latestAccount
      };

    /**
     * stateaccount ,payload address
     * **/
    case GET_DATA_FROM_LOCALSTORAGE:
      let account = getAccountFromLocal(payload.address);
      /**
       * localStorage rememberMe === true
       */
      const rememberMe = !!account;
      account = account ? account : { address: payload.address };
      /**
       * LOGGED_IN localstorage
       * Reset Password
       *
       * localstorage ,Reset Password 
       *
       */
      if (
        getAllAccountStates().includes(account.state) === false ||
        account.state === WALLET_UNCONNECTED
      ) {
        account.state = NOT_REGISTERED;
      }
      saveLoginRecord();

      return {
        ...state,
        account,
        rememberMe
      };

    case CHANGE_ACCOUNT:
      return {
        ...state,
        account: { state: WALLET_UNCONNECTED }
      };

    case LOGOUT:
      removeAccountFromLocal(state.account.address);
      return {
        account: {
          ...state.account,
          state: REGISTERED
        },
        rememberMe: false
      };

    default:
      return state;
  }
};
