import { connect } from 'react-redux';
import I from 'components/I';
import React from 'react';
import styled from 'styled-components';

import colors from 'colors';

import { ConfigProvider, Pagination, Table, Tooltip } from 'antd';
import Moment from 'moment';

import {
  LargeTableContainer,
  LargeTableRow,
  LargeTableRowFailed,
  LargeTableRowProcessed,
  LargeTableRowProcessing,
  TextCompactTableHeader
} from 'styles/Styles';
import EmptyTableIndicator from 'components/EmptyTableIndicator';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

import { faCheckCircle } from '@fortawesome/free-solid-svg-icons/faCheckCircle';
import { faCircleNotch } from '@fortawesome/free-solid-svg-icons/faCircleNotch';
import { faClock } from '@fortawesome/free-solid-svg-icons/faClock';
import { faExclamationCircle } from '@fortawesome/free-solid-svg-icons/faExclamationCircle';

import './BalanceTable.less'; // TODO: remove this dependency
import { getEtherscanLink } from 'lightcone/api/localStorgeAPI';

const StatusFontAwesomeIcon = styled(FontAwesomeIcon)`
  margin-right: 4px;
`;
class DepositWithdrawalBaseTable extends React.Component {
  render() {
    const customizeRenderEmpty = () => (
      <EmptyTableIndicator text={this.props.placeHolder} />
    );

    const columns = [
      {
        title: '',
        dataIndex: 'padding',
        width: 14
      },
      {
        title: (
          <TextCompactTableHeader>
            <I s="Asset" />
          </TextCompactTableHeader>
        ),
        dataIndex: 'symbol'
      },
      {
        title: (
          <TextCompactTableHeader>
            <I s="Amount" />
          </TextCompactTableHeader>
        ),
        dataIndex: 'amount'
      },
      {
        title: (
          <TextCompactTableHeader>
            <I s="Fee" />
          </TextCompactTableHeader>
        ),
        dataIndex: 'fee'
      },
      {
        title: (
          <TextCompactTableHeader>
            <I s="Tx Hash" />
          </TextCompactTableHeader>
        ),
        dataIndex: 'txHash',
        width: 180
      },
      {
        title: (
          <TextCompactTableHeader>
            <I s="Timestamp" />
          </TextCompactTableHeader>
        ),
        dataIndex: 'date',
        width: 180
      },
      {
        title: (
          <TextCompactTableHeader
            style={{
              paddingRight: '64px'
            }}
          >
            <I s="Status" />
          </TextCompactTableHeader>
        ),
        dataIndex: 'status'
      }
    ];

    const data = [];
    for (let i = 0; i < this.props.data.length; i++) {
      const transaction = this.props.data[i];
      var status = '-';
      if (transaction.status === 'processing') {
        status = (
          <Tooltip
            placement="bottom"
            overlayStyle={{
              color: colors.textWhite,
              fontSize: '0.85rem'
            }}
            title={
              <div>
                <p>
                  <I s={'StatusProcessing'} />
                </p>
                <p>
                  <I s={'StatusProcessing2'} />
                </p>
              </div>
            }
          >
            <LargeTableRowProcessing style={{ color: colors.highlight }}>
              <StatusFontAwesomeIcon icon={faCircleNotch} spin />
              <div>
                <I s="Processing" />
              </div>
            </LargeTableRowProcessing>
          </Tooltip>
        );
      } else if (transaction.status === 'processed') {
        status = (
          <LargeTableRowProcessed style={{ color: colors.green }}>
            <StatusFontAwesomeIcon icon={faCheckCircle} />
            <div>
              <I s="Succeeded" />
            </div>
          </LargeTableRowProcessed>
        );
      } else if (transaction.status === 'failed') {
        status = (
          <LargeTableRowFailed style={{ color: colors.red }}>
            <StatusFontAwesomeIcon icon={faExclamationCircle} />
            <div>
              <I s="Failed" />
            </div>
          </LargeTableRowFailed>
        );
      } else if (transaction.status === 'received') {
        status = (
          <Tooltip
            placement="bottom"
            overlayStyle={{
              color: colors.textWhite,
              fontSize: '0.85rem'
            }}
            title={<I s={'StatusConfirming'} />}
          >
            <LargeTableRowProcessing style={{ color: colors.orange }}>
              <StatusFontAwesomeIcon icon={faClock} />

              <div>
                <I s="Confirming" />
              </div>
            </LargeTableRowProcessing>
          </Tooltip>
        );
      }
      data.push({
        key: i,
        symbol: (
          <LargeTableRow>
            <I s={transaction.tokenName} />
          </LargeTableRow>
        ),
        amount: (
          <LargeTableRow>
            {transaction.amountInUI} {transaction.symbol}
          </LargeTableRow>
        ),
        fee: (
          <LargeTableRow
            style={{
              color: colors.textDim
            }}
          >
            {transaction.feeInUI} ETH
          </LargeTableRow>
        ),
        date: (
          <LargeTableRow
            style={{
              color: colors.textDim
            }}
          >
            {Moment(transaction.timestamp).format('YYYY-MM-DD HH:mm:ss')}
          </LargeTableRow>
        ),
        txHash: (
          <LargeTableRow>
            <a
              href={`${getEtherscanLink(this.props.chainId)}/tx/${
                transaction.txHash
              }`}
              target="_blank"
              rel="noopener noreferrer"
            >
              {transaction.txHashInUI}
            </a>
          </LargeTableRow>
        ),
        status: (
          <div
            style={{
              paddingRight: '14px'
            }}
          >
            {status}
          </div>
        )
      });
    }

    const hasPagination = this.props.total > this.props.limit;

    return (
      <LargeTableContainer className={'my-table'}>
        <ConfigProvider renderEmpty={data.length === 0 && customizeRenderEmpty}>
          <Table
            style={{
              borderStyle: 'solid',
              borderWidth: '1px',
              borderColor: colors.foreground,
              height: `${data.length * 34 + 35}px`,
              minHeight: '500px'
            }}
            size="middle"
            columns={columns}
            dataSource={data}
            pagination={false}
            scroll={{
              y: `${data.length * 34}px`
            }}
          />
          {hasPagination ? (
            <Pagination
              style={{
                padding: '30px 0px 30px 0px',
                background: colors.background,
                textAlign: 'center'
              }}
              size=""
              total={this.props.total}
              current={this.props.current}
              onChange={this.props.onChange}
              pageSize={this.props.limit}
            />
          ) : (
            <div />
          )}
        </ConfigProvider>
      </LargeTableContainer>
    );
  }
}

const mapStateToProps = state => {
  const { exchange } = state;
  return { chainId: exchange.chainId };
};

export default connect(mapStateToProps, null)(DepositWithdrawalBaseTable);
