import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCheck } from '@fortawesome/free-solid-svg-icons/faCheck';
import { faTimes } from '@fortawesome/free-solid-svg-icons/faTimes';
import React from 'react';
import colors from 'colors';
import styled from 'styled-components';

const MyFontAwesomeIcon = styled(FontAwesomeIcon)`
  display: inline-block;
  width: 16px !important;
`;

const PasswordRule = ({ ruleText, password, validator, aggregator }) => {
  const passed = validator(password);
  aggregator(passed);

  const getIcon = (password, passed) => {
    if (password === '')
      return <MyFontAwesomeIcon icon={faCheck} style={{ color: 'inherit' }} />;
    else if (passed)
      return (
        <MyFontAwesomeIcon icon={faCheck} style={{ color: colors.green }} />
      );
    else
      return <MyFontAwesomeIcon icon={faTimes} style={{ color: colors.red }} />;
  };

  return (
    <p
      style={{
        fontSize: '0.9rem',
        margin: '4px 0'
      }}
    >
      {getIcon(password, passed)}
      <span> {ruleText} </span>
    </p>
  );
};
export default PasswordRule;
