import { getBalances } from 'lightcone/api/v1/balances';
import { getEthBalance } from 'lightcone/api/v1/ethBalance';
import { getTokenBalance } from 'lightcone/api/v1/tokenBalance';

import {
  getDepositHistory,
  getWithdrawalHistory
} from 'lightcone/api/LightconeAPI';

export const UPDATE_MY_BALANCES = 'UPDATE_MY_BALANCES';
export const UPDATE_ETH_BALANCE = 'UPDATE_ETH_BALANCE';

export const UPDATE_TOKEN_BALANCE = 'UPDATE_TOKEN_BALANCE';
export const UPDATE_BALANCE = 'UPDATE_BALANCE';

export const EMPTY_BALANCES = 'EMPTY_BALANCES';

export const UPDATE_DEPOSITS = 'UPDATE_DEPOSITS';
export const EMPTY_DEPOSITS = 'EMPTY_DEPOSITS';

export const UPDATE_WITHDRAWALS = 'UPDATE_WITHDRAWALS';
export const EMPTY_WITHDRAWALS = 'EMPTY_WITHDRAWALS';

export function emptyBalances() {
  return {
    type: EMPTY_BALANCES,
    payload: {}
  };
}

export function updateBalance(balance) {
  return {
    type: UPDATE_BALANCE,
    payload: {
      balance
    }
  };
}

export function updateMyAccountPage(balances) {
  return {
    type: UPDATE_MY_BALANCES,
    payload: {
      balances
    }
  };
}

export function fetchMyAccountPage(accountId, apiKey, tokens, skip, limit) {
  return dispatch => {
    (async () => {
      try {
        const balances = await getBalances(
          accountId,
          apiKey,
          tokens,
          skip,
          limit
        );
        dispatch(updateMyAccountPage(balances));
      } catch (error) {
        console.log(error);
      }
    })();
  };
}

export function updateEthBalance(balance, tokens) {
  return {
    type: UPDATE_ETH_BALANCE,
    payload: {
      balance,
      tokens
    }
  };
}

export function fetchEthBalance(address) {
  return dispatch => {
    (async () => {
      try {
        const balance = await getEthBalance(address);
        dispatch(updateEthBalance(balance));
      } catch (error) {}
    })();
  };
}

export function updateTokenBalance(balance, symbol, tokens) {
  return {
    type: UPDATE_TOKEN_BALANCE,
    payload: {
      balance,
      symbol,
      tokens
    }
  };
}

export function fetchTokenBalance(owner, symbol, tokens) {
  return dispatch => {
    (async () => {
      try {
        const balance = await getTokenBalance(owner, symbol, tokens);
        dispatch(updateTokenBalance(balance, symbol));
      } catch (error) {}
    })();
  };
}

export function updateDeposits(payload) {
  return {
    type: UPDATE_DEPOSITS,
    payload
  };
}

export function fetchDeposits(limit, offset, accountId, apiKey, tokens) {
  return dispatch => {
    (async () => {
      try {
        const response = await getDepositHistory(
          accountId,
          limit,
          offset,
          apiKey,
          tokens
        );
        dispatch(updateDeposits(response));
      } catch (error) {}
    })();
  };
}

export function emptyDeposits() {
  return {
    type: EMPTY_DEPOSITS,
    payload: {}
  };
}

export function updateWithdrawals(payload) {
  return {
    type: UPDATE_WITHDRAWALS,
    payload
  };
}

export function fetchWithdrawals(limit, offset, accountId, apiKey, tokens) {
  return dispatch => {
    (async () => {
      try {
        const response = await getWithdrawalHistory(
          accountId,
          limit,
          offset,
          apiKey,
          tokens
        );
        dispatch(updateWithdrawals(response));
      } catch (error) {}
    })();
  };
}

export function emptyWithdrawals() {
  return {
    type: EMPTY_WITHDRAWALS,
    payload: {}
  };
}
