import { Col, Row } from 'antd';
import React from 'react';
import colors from 'colors';

const LabelValue = ({ label, value, unit, onClick = () => {} }) => {
  return (
    <Row
      gutter={16}
      style={{
        padding: '4px 0',
        fontSize: '0.85rem'
      }}
      onClick={onClick}
    >
      <Col
        span={12}
        style={{
          color: colors.textDim
        }}
      >
        {label}
      </Col>
      <Col
        span={12}
        style={{
          textAlign: 'right',
          color: colors.textWhite
        }}
      >
        {value}

        {unit ? (
          <span
            style={{
              minWidth: '40px',
              textAlign: 'right',
              userSelect: 'none',
              paddingLeft: '4px'
            }}
          >
            {unit}
          </span>
        ) : (
          <span />
        )}
      </Col>
    </Row>
  );
};

export default LabelValue;
