import { connect } from 'react-redux';
import I from 'components/I';
import React from 'react';
import colors from 'colors';

import { Button, Col, Row } from 'antd';
import { updateTradeType } from 'redux/actions/TradePanel';

class TradeTabButtons extends React.Component {
  clickedBuyButton = () => {
    this.props.updateTradeType('buy');
  };

  clickedSellButton = () => {
    this.props.updateTradeType('sell');
  };

  render() {
    const isBuy = this.props.tradeType === 'buy';
    const isSell = this.props.tradeType === 'sell';

    const buttonStyle = {
      color: colors.textBright,
      borderStyle: 'none',
      height: '32px',
      borderRadius: '4px',
      fontWeight: '600',
      fontSize: '0.85rem',
      textTransform: 'uppercase'
    };

    const buyButtonActiveStyle = {
      ...buttonStyle,
      background: this.props.buyPrimaryColor
    };

    const sellButtonActiveStyle = {
      ...buttonStyle,
      background: this.props.sellPrimaryColor
    };

    const buttonInactiveStyle = {
      ...buttonStyle,
      backgroundColor: colors.buttonBackground,
      color: colors.textWhite,
      height: '32px',
      borderRadius: '4px'
    };

    return (
      <div>
        <Row
          gutter={0}
          style={{
            paddingTop: '0px',
            paddingBottom: '0px',
            color: colors.textWhite,
            backgroundColor: colors.buttonBackground,
            borderRadius: '4px'
          }}
        >
          <Col className="columnLeft" span={12} style={{}}>
            <Button
              className={isBuy ? 'deposit-button' : 'logout-button'}
              block
              style={isBuy ? buyButtonActiveStyle : buttonInactiveStyle}
              onClick={this.clickedBuyButton}
            >
              <I s="Buy" />
            </Button>
          </Col>
          <Col className="columnRight" span={12}>
            <Button
              className={isSell ? 'withdraw-button' : 'logout-button'}
              block
              style={isSell ? sellButtonActiveStyle : buttonInactiveStyle}
              onClick={this.clickedSellButton}
            >
              <I s="Sell" />
            </Button>
          </Col>
        </Row>
      </div>
    );
  }
}

const mapStateToProps = state => {
  const { colorManager } = state;
  const { tradeType } = state.tradePanel;
  return {
    buyPrimaryColor: colorManager.buyPrimary,
    sellPrimaryColor: colorManager.sellPrimary,
    tradeType
  };
};

const mapDispatchToProps = dispatch => {
  return {
    updateTradeType: tradeType => dispatch(updateTradeType(tradeType))
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(TradeTabButtons);
