import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import I from 'components/I';
import React from 'react';
import colors from 'colors';

import { faCircleNotch } from '@fortawesome/free-solid-svg-icons/faCircleNotch';

const ModalIndicator = ({ title, tips, marginTop, imageUrl }) => {
  const tips_ = tips || [];
  const marginTop_ = marginTop || '80px';
  return (
    <div>
      <FontAwesomeIcon
        icon={faCircleNotch}
        size="2x"
        spin
        style={{ margin: '20px', marginTop: marginTop_ }}
      />

      <div
        style={{
          margin: '16px',
          color: colors.orange,
          fontSize: '1rem'
        }}
      >
        <I s={title} />
      </div>

      <div
        style={{
          fontSize: '0.85rem',
          fontColor: colors.textWhite
        }}
      >
        {tips_.map((tip, i) => (
          <div
            style={{
              textAlign: 'left',
              margin: '16px'
            }}
            key={i}
          >
            {tip}
          </div>
        ))}
      </div>

      {imageUrl ? (
        <div>
          <img
            alt="check metamask plugin icon"
            style={{
              userSelect: 'none',
              height: '80px',
              marginTop: '16px',
              boxShadow: '0 0 10px 10px rgba(0, 0, 0, 0.1)'
            }}
            src={imageUrl}
          />
        </div>
      ) : (
        <span />
      )}
    </div>
  );
};

export default ModalIndicator;
