import {
  INITIALIZE_INFO,
  UPDATE_INFO,
  UPDATE_MARKETS,
  UPDATE_TOKENS
} from '../actions/ExchangeInfo';

const initialState = {
  isInitialized: false,
  chainId: 0,
  exchangeId: 0,
  exchangeAddress: '',
  onchainFees: [],
  markets: [
    {
      market: 'LRC-ETH',
      baseTokenId: 2,
      quoteTokenId: 0,
      precisionForPrice: 6,
      orderbookAggLevels: 5,
      enabled: true
    }
  ],
  tokens: [
    {
      type: 'ETH',
      tokenId: 0,
      symbol: 'ETH',
      name: 'Ethereum',
      address: '0x0000000000000000000000000000000000000000',
      unit: '',
      decimals: 18,
      precision: 6,
      minOrderAmount: '50000000000000000',
      maxOrderAmount: '50000000000000000000',
      dustOrderAmount: '500000000000000'
    },
    {
      type: 'ERC20',
      tokenId: 2,
      symbol: 'LRC',
      name: 'Loopring',
      address: '0x6621fc9ca6cb25dd1de695fb1221d12350d7d318',
      unit: '',
      decimals: 18,
      precision: 6,
      minOrderAmount: '500000000000000000000',
      maxOrderAmount: '500000000000000000000000',
      dustOrderAmount: '5000000000000000000'
    }
  ]
};

export const ExchangeInfoReducer = (state = initialState, action) => {
  switch (action.type) {
    case INITIALIZE_INFO:
      return {
        ...action.payload,
        isInitialized: true
      };
    case UPDATE_INFO:
      return {
        ...state,
        ...action.payload
      };

    case UPDATE_TOKENS:
      return {
        ...state,
        ...action.payload
      };

    case UPDATE_MARKETS:
      return {
        ...state,
        ...action.payload
      };
    default:
      return state;
  }
};
