import { Layout } from 'antd';
import { Scroller } from 'styles/Styles';
import { connect } from 'react-redux';

import AppLayout from 'AppLayout';

import React from 'react';

import colors from 'colors';
const { Content, Sider } = Layout;

class LegalTemplate extends React.Component {
  render() {
    return (
      <div>
        <Layout
          hasSider
          style={{
            height: AppLayout.simpleSecondaryPageHeight
          }}
        >
          <Sider
            width={AppLayout.tradePanelWidth}
            style={{
              borderTop: '1px solid ' + colors.foreground,
              padding: '0px',
              background: colors.sidePanelBackground,
              borderStyle: 'none'
            }}
            trigger={null}
            breakpoint="sm"
            collapsedWidth="0"
            onBreakpoint={broken => {}}
            onCollapse={(collapsed, type) => {
              this.props.updateColumns();
            }}
          >
            <Scroller
              style={{
                borderTop: '1px solid ' + colors.foreground
              }}
            >
              {this.props.navigation}
            </Scroller>
          </Sider>
          <Content
            width="100%"
            style={{
              backgroundColor: colors.background,
              borderLeftStyle: 'none'
            }}
          >
            <div
              style={{
                marginLeft: 'auto',
                marginRight: 'auto',
                width: '740px',
                overflow: 'scroll',
                height: '100%',
                background:
                  'url(/assets/images/bars.svg) center center no-repeat',
                backgroundSize: '40px 40px',
                backgroundColor: colors.background
              }}
            >
              <iframe
                title="termly"
                src={this.props.src}
                frameBorder="0"
                style={{
                  overflow: 'hidden',
                  height: '100%',
                  width: '100%'
                }}
              ></iframe>
            </div>
          </Content>
        </Layout>
      </div>
    );
  }
}

const mapStateToProps = state => {
  const { layoutManager } = state;
  return { layoutManager };
};

export default connect(mapStateToProps, null)(LegalTemplate);
