import { HighlightTextSpan, RegularTextSpan } from 'styles/Styles';
import { connect } from 'react-redux';
import AppLayout from 'AppLayout';
import I from 'components/I';
import React from 'react';
import colors from 'colors';
import styled from 'styled-components';

import { Button, Menu, Popover } from 'antd';
import MarketSelector from 'modals/market-selector/MarketSelector';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faDotCircle } from '@fortawesome/free-solid-svg-icons/faDotCircle';
import { fetchTicker, restTicker } from 'redux/actions/market/Ticker';

import { tracker } from 'components/DefaultTracker';

const SelectMarketButton = styled(Button)`
  border-style: none !important;
  background-color: #292c42;
  color: ${colors.textWhite};
  height: 46px !important;
  font-size: 0.9rem !important;
  font-weight: 600 !important;

  &:hover {
    background: ${colors.background} !important;
  }

  &:focus {
    background: ${colors.background} !important;
  }
`;

const BlinkFontAwesomeIcon = styled(FontAwesomeIcon)`
  @keyframes blinker {
    50% {
      opacity: 0.67;
      filter: drop-shadow(0 0 0px ${colors.green});
    }
  }
  margin-right: 8px;
  filter: drop-shadow(0 0 8px ${colors.green});
  animation: blinker 3s ease-in-out infinite;
`;

const MenuItem = styled(Menu.Item)`
  cursor: 'default';
  padding-left: 10px;
  line-height: ${AppLayout.tickerBarHeight};
`;

const MenuHighlightTextSpan = styled(HighlightTextSpan)`
  padding-left: 12px;
`;

class TickerBar extends React.Component {
  constructor(props, context) {
    super(props, context);
    this.setModalVisible = this.setModalVisible.bind(this);
    this.handleClickChange = this.handleClickChange.bind(this);
  }

  state = {
    modalVisible: false
  };

  componentDidMount() {
    this.props.fetchTicker(this.props.currentMarket.current, this.props.tokens);
  }

  componentDidUpdate(prevProps, prevState) {
    if (
      prevProps &&
      prevProps.currentMarket &&
      prevProps.currentMarket.current &&
      prevProps.currentMarket.current !== this.props.currentMarket.current
    ) {
      this.props.restTicker();
      this.props.fetchTicker(
        this.props.currentMarket.current,
        this.props.tokens
      );
    }
  }

  setModalVisible(modalVisible) {
    this.setState({ modalVisible });
  }

  handleClickChange(visible) {
    this.setState({
      modalVisible: visible
    });
    tracker.trackEvent({
      type: 'LOCATION_CHANGE',
      data: {
        location: 'market-selection',
        visible: visible
      }
    });
  }

  render() {
    const { ticker, currentMarket } = this.props;

    // Find base price
    const { prices } = this.props.cmcPrice;
    let price = '-';
    let priceUnit = this.props.cmcPrice.legal;
    let priceLabel = <I s="Last Price" />;

    try {
      const result = prices.filter(
        x => x.symbol === currentMarket.baseTokenSymbol
      );
      price = result[0]['price'];
    } catch (error) {}

    return (
      <div
        style={{
          borderBottomStyle: 'none'
        }}
      >
        <Menu
          selectedKeys={[]}
          mode="horizontal"
          theme="dark"
          style={{
            background: colors.foreground,
            lineHeight: AppLayout.tickerBarHeight
          }}
        >
          <Menu.Item
            key="current-market"
            style={{
              width: AppLayout.tradePanelWidth,
              height: '46px',
              background: '#303249',
              paddingLeft: AppLayout.sidePadding,
              paddingRight: AppLayout.sidePadding
            }}
          >
            <Popover
              overlayClassName="marketSelection"
              placement="bottomLeft"
              title={null}
              content={
                <MarketSelector
                  isVisible={this.state.modalVisible}
                  closePopover={() => {
                    this.setModalVisible(false);
                  }}
                />
              }
              trigger="click"
              visible={this.state.modalVisible}
              onVisibleChange={this.handleClickChange}
            >
              <SelectMarketButton
                block
                onClick={() => this.setModalVisible(true)}
              >
                <span
                  style={{
                    width: '94%',
                    textAlign: 'left'
                  }}
                >
                  {currentMarket.current}
                </span>
                <svg width="15px" height="9px" viewBox="0 0 10 6">
                  <defs>
                    <linearGradient
                      x1="0%"
                      y1="-21.957624%"
                      x2="100%"
                      y2="100%"
                      id="linearGradient-1"
                    >
                      <stop stopColor="#00AEF3" offset="0%"></stop>
                      <stop stopColor="#3750FF" offset="100%"></stop>
                    </linearGradient>
                  </defs>
                  <g
                    id=""
                    stroke="none"
                    strokeWidth="1"
                    fill="none"
                    fillRule="evenodd"
                  >
                    <g
                      id=""
                      transform="translate(-230.000000, -23.000000)"
                      fill="url(#linearGradient-1)"
                    >
                      <g id="-2">
                        <g transform="translate(8.000000, 13.000000)">
                          <g
                            id="Icon/right-Arrow"
                            transform="translate(227.000000, 11.000000) rotate(-270.000000) translate(-227.000000, -11.000000) translate(223.500000, 6.000000)"
                          >
                            <g
                              transform="translate(3.500000, 5.000000) rotate(-270.000000) translate(-3.500000, -5.000000) translate(-1.000000, 2.000000)"
                              id="Icon/Up-Arrow"
                            >
                              <path d="M0.120616938,5.13596683 L4.1871372,0.18512069 C4.36855783,-0.0357525857 4.68889497,-0.0628236186 4.90263022,0.124655845 C4.92605459,0.145202709 4.9476254,0.167904898 4.96707703,0.192482859 L8.88530375,5.143329 C9.06278977,5.36759014 9.03074631,5.69807569 8.81373269,5.88148914 C8.72305225,5.95812954 8.60951179,6 8.49236581,6 L0.50761884,6 C0.227268696,6 0,5.76514131 0,5.47542843 C0,5.35107753 0.0427473669,5.23077031 0.120616938,5.13596683 Z"></path>
                            </g>
                          </g>
                        </g>
                      </g>
                    </g>
                  </g>
                </svg>
              </SelectMarketButton>
            </Popover>
          </Menu.Item>
          <MenuItem
            key="current-market-trade-price"
            selectable={false}
            style={{
              paddingLeft: '12px'
            }}
          >
            <RegularTextSpan>{priceLabel}</RegularTextSpan>
            <MenuHighlightTextSpan>
              {ticker.close} {currentMarket.quoteTokenSymbol} ({price}{' '}
              {priceUnit})
            </MenuHighlightTextSpan>
          </MenuItem>
          <MenuItem key="current-market-trade-change" selectable={false}>
            <RegularTextSpan>
              <I s="24h Change" />
            </RegularTextSpan>
            <MenuHighlightTextSpan
              style={{
                color: ticker.percentChange24h.startsWith('-')
                  ? this.props.colorManager.down
                  : this.props.colorManager.up
              }}
            >
              {ticker.percentChange24h}
            </MenuHighlightTextSpan>
          </MenuItem>
          <MenuItem key="current-market-trade-volume" selectable={false}>
            <RegularTextSpan>
              <I s="24h Volume" />
            </RegularTextSpan>
            <MenuHighlightTextSpan>
              {ticker.volume} {currentMarket.quoteTokenSymbol}
            </MenuHighlightTextSpan>
          </MenuItem>
          <MenuItem key="current-market-trade-high" selectable={false}>
            <RegularTextSpan>
              <I s="High" />
            </RegularTextSpan>
            <MenuHighlightTextSpan>
              {ticker.high} {currentMarket.quoteTokenSymbol}
            </MenuHighlightTextSpan>
          </MenuItem>

          <MenuItem key="current-market-trade-low">
            <RegularTextSpan>
              <I s="Low" />
            </RegularTextSpan>
            <MenuHighlightTextSpan>
              {ticker.low} {currentMarket.quoteTokenSymbol}
            </MenuHighlightTextSpan>
          </MenuItem>

          <Menu.Item
            key="market-available"
            style={{
              float: 'right',
              paddingLeft: '0px',
              paddingRight: '22px',
              cursor: 'default'
            }}
          >
            <span
              style={{
                paddingLeft: '8px',
                fontSize: '0.85rem',
                userSelect: 'none',
                color: colors.green
              }}
            >
              <BlinkFontAwesomeIcon icon={faDotCircle} />
              <I s="Market Available" />
            </span>
          </Menu.Item>
        </Menu>
      </div>
    );
  }
}

const mapStateToProps = state => {
  const { ticker, currentMarket, cmcPrice, colorManager, exchange } = state;
  return {
    ticker,
    currentMarket,
    cmcPrice,
    colorManager,
    tokens: exchange.tokens
  };
};

const mapDispatchToProps = dispatch => {
  return {
    restTicker: () => dispatch(restTicker()),
    fetchTicker: (market, tokens) => dispatch(fetchTicker(market, tokens))
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(TickerBar);
