import { connect } from 'react-redux';
import I from 'components/I';
import React from 'react';

import { List } from 'antd';
import { getTicker } from 'lightcone/api/LightconeAPI';
import { history } from 'redux/configureStore';
import colors from 'colors';
import config from 'lightcone/config';
import styled from 'styled-components';

import './MarketSelector.less';
import { getEtherscanLink } from 'lightcone/api/localStorgeAPI';
import ColumnWrapper from './components/ColumnWrapper';

const AssetIcon = styled.a`
  display: inline-block;
  width: 42px;
  height: 28px;
  border-radius: 50%;
  margin-right: 16px;
  margin-left: -8px;
  margin-top: -8px;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  background-origin: content-box;
`;

class MarketSelector extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      filter: '',
      tickers: []
    };
  }

  componentDidMount() {
    this.loadData();
  }

  componentDidUpdate(prevProps, prevState) {
    if (
      this.props.isVisible !== false &&
      this.props.isVisible !== prevProps.isVisible
    ) {
      this.loadData();
    }
  }

  loadData() {
    (async () => {
      try {
        const tickers = await getTicker(
          this.props.market.marketList.marketNames,
          this.props.tokens
        );
        this.setState({
          tickers
        });
      } catch (error) {}
    })();
  }

  getAssetUrl(token, exchangeAddress) {
    var addr = window.wallet ? window.wallet.address : exchangeAddress;
    if (token.symbol === 'ETH')
      return `${getEtherscanLink(this.props.chainId)}/address/${addr}`;
    else
      return `${getEtherscanLink(this.props.chainId)}/token/${
        token.address
      }?a=${addr}`;
  }
  getAssetIconUrl(token) {
    var path;

    if (token.symbol === 'ETH') {
      path = 'info';
    } else if (token.symbol === 'LRC') {
      return 'url(/assets/images/LRC.png)';
    } else {
      path = 'assets/' + token.address;
    }
    return 'url(/assets/images/ethereum/' + path + '/logo.png)';
  }

  render() {
    const { filter } = this.state;
    const { tokens } = this.props;
    const markets =
      filter !== ''
        ? this.props.market.marketList.markets.filter(x =>
            x.markets.includes(filter.toUpperCase())
          )
        : this.props.market.marketList.markets;
    let updatedMarkets = [];
    for (let i = 0; i < markets.length; i = i + 1) {
      const market = markets[i];
      const tickers = this.state.tickers.filter(
        x => x.market === market.market
      );

      let updatedMarket = {
        ...market,
        baseToken: config.getTokenBySymbol(market.market.split('-')[0], tokens),
        quoteToken: config.getTokenBySymbol(market.market.split('-')[1], tokens)
      };
      if (tickers.length === 1) {
        updatedMarket['ticker'] = tickers[0];
      } else {
        updatedMarket['ticker'] = {
          percentChange24h: '-',
          volume: '-',
          close: '-'
        };
      }
      updatedMarkets.push(updatedMarket);
    }

    return (
      <div
        style={{
          width: '400px'
        }}
      >
        <div
          style={{
            paddingTop: '8px'
          }}
        >
          <List
            bordered
            dataSource={updatedMarkets}
            renderItem={updatedMarket => (
              <div
                onClick={() => {
                  if (this.props.market !== updatedMarket) {
                    history.push('/trade/' + updatedMarket.market);
                    this.props.closePopover();
                  }
                }}
              >
                <List.Item
                  className="market-selector-list-item"
                  style={{
                    backgroundColor: colors.background,
                    borderTopStyle: 'none',
                    marginBottom: '8px',
                    height: '56px',
                    borderRadius: '4px',
                    cursor: 'pointer'
                  }}
                >
                  <AssetIcon
                    href={this.getAssetUrl(
                      updatedMarket.baseToken,
                      this.props.exchangeAddress
                    )}
                    target="_blank"
                    style={{
                      backgroundImage: this.getAssetIconUrl(
                        updatedMarket.baseToken
                      )
                    }}
                  />

                  <span
                    style={{
                      width: '20%',
                      minWidth: '120px'
                    }}
                  >
                    <ColumnWrapper
                      textAlign="left"
                      row1={updatedMarket.market}
                      row2={<I s={updatedMarket.baseToken.name} />}
                    />
                  </span>
                  <span
                    style={{
                      width: '46%',
                      marginRight: '4%'
                    }}
                  >
                    <ColumnWrapper
                      textAlign="right"
                      row1={`${updatedMarket.ticker.volume} ${updatedMarket.quoteToken.symbol}`}
                      row2={<I s="24h Volume" />}
                    />
                  </span>
                  <span
                    style={{
                      width: '30%'
                    }}
                  >
                    <ColumnWrapper
                      textAlign="right"
                      row1={
                        updatedMarket.ticker.close
                          ? updatedMarket.ticker.close
                          : '-'
                      }
                      row2={`${updatedMarket.ticker.percentChange24h}%`}
                      row2Color={
                        updatedMarket.ticker.percentChange24h &&
                        updatedMarket.ticker.percentChange24h.startsWith('-')
                          ? this.props.colorManager.down
                          : this.props.colorManager.up
                      }
                    />
                  </span>
                </List.Item>
              </div>
            )}
          />
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => {
  const { colorManager, market, exchange } = state;

  return {
    colorManager,
    chainId: exchange.chainId,
    market,
    markets: exchange.markets,
    tokens: exchange.tokens,
    exchangeAddress: exchange.exchangeAddress
  };
};

const mapDispatchToProps = dispatch => {
  return {};
};

export default connect(mapStateToProps, mapDispatchToProps)(MarketSelector);
