import { UPDATE_CANDLE } from 'redux/actions/market/Candle';

// Hack: initial State can not be empty.
const initialState = {
  ohlc: []
};

export const CandlesReducer = (state = initialState, action) => {
  switch (action.type) {
    case UPDATE_CANDLE:
      const ohlc = action.payload.ohlc;
      return {
        ...state,
        ohlc
      };
    default:
      return state;
  }
};
