import PropTypes from 'prop-types';
import React from 'react';
import styled from 'styled-components';

import OrderSizeBar from './OrderSizeBar';
import colors from 'colors';

const CompactOrderTableRow = styled.tr`
  font-size: 0.85rem;
  color: ${colors.textDim};
  height: 24px;
  font-weight: 600;
  background: ${colors.foreground};
  &:hover {
    cursor: pointer;
    background: ${colors.background};
  }
`;

const CompactOrderTableSizeColumn = styled.td`
  text-align: right;
  width: 30%;
  padding: 1px 5% 1px 1px;
`;

const CompactOrderTableSizePrice = styled.td`
  text-align: left;
  width: 30%;
  padding: 1px 1px 1px 1px;
`;

const CompactOrderTablePositionColumn = styled.td`
  text-align: right;
  width: 30%;
  padding: 1px 12px 1px 1px;
`;

const EmptyOrderRow = () => {
  return (
    <CompactOrderTableRow style={{ userSelect: 'none' }}>
      <td />
      <CompactOrderTableSizeColumn
        style={{
          textAlign: 'left'
        }}
      >
        -
      </CompactOrderTableSizeColumn>
      <CompactOrderTableSizePrice
        style={{
          textAlign: 'center'
        }}
      >
        -
      </CompactOrderTableSizePrice>
      <CompactOrderTablePositionColumn>-</CompactOrderTablePositionColumn>
    </CompactOrderTableRow>
  );
};

const OrderRow = ({
  showSizeBar,
  side,
  size,
  filled,
  sizeBarMaxWidth,
  sizeBarMaxSize,
  sizeBarUnitSize,
  buyPrimary,
  buyBar,
  sellPrimary,
  sellBar,
  onClick,
  order,
  dataConfigs,
  onClickCancel,
  ...props
}) => {
  return (
    <CompactOrderTableRow
      {...props}
      onClick={e => {
        e.preventDefault();
        if (onClick) {
          onClick(order, side);
        }
      }}
    >
      <OrderSizeBar
        {...{
          showSizeBar,
          side,
          size,
          sizeBarMaxWidth,
          sizeBarMaxSize,
          sizeBarUnitSize,
          buyPrimary,
          buyBar,
          sellPrimary,
          sellBar
        }}
      />

      {dataConfigs.map(({ propName = 'data', format, getter, renderer }, i) => {
        if (propName === 'price') {
          return (
            <CompactOrderTableSizePrice key={i}>
              {renderer({
                side,
                format,
                [propName]: getter(order),
                buyPrimary,
                sellPrimary
              })}
            </CompactOrderTableSizePrice>
          );
        } else if (propName === 'size') {
          return (
            <CompactOrderTableSizeColumn key={i}>
              {renderer({
                side,
                format,
                [propName]: getter(order)
              })}
            </CompactOrderTableSizeColumn>
          );
        } else if (propName === 'position') {
          return (
            <CompactOrderTablePositionColumn key={i}>
              {renderer({
                side,
                format,
                [propName]: getter(order)
              })}
            </CompactOrderTablePositionColumn>
          );
        } else {
          return <span />;
        }
      })}
    </CompactOrderTableRow>
  );
};

OrderRow.propTypes = {
  side: PropTypes.oneOf(['buy', 'sell']),
  showSizeBar: PropTypes.bool,
  dataConfigs: PropTypes.arrayOf(PropTypes.object)
};

OrderRow.defaultProps = {
  side: 'buy',
  showSizeBar: true,
  dataConfigs: []
};

export { OrderRow, EmptyOrderRow };
