import { connect } from 'react-redux';
import I from 'components/I';
import React from 'react';

import colors from 'colors';
import config from 'lightcone/config';

import { Col, Row } from 'antd';

import AssetPanelButtons from './components/AssetPanelButtons';

import './AssetPanel.less';

class AssetPanel extends React.Component {
  getAvailableAmount = (token, balances) => {
    const holdBalance = balances.find(ba => ba.tokenId === token.tokenId);
    return holdBalance
      ? holdBalance.available
      : Number(0).toFixed(token.precision);
  };

  getAvailableTotalInFiat = (token, availableAmount, prices, tokens) => {
    if (prices) {
      try {
        const baseTokenPrice = prices.filter(x => x.symbol === token.symbol)[0][
          'price'
        ];
        let availableTotal = (
          parseFloat(baseTokenPrice) * parseFloat(availableAmount)
        ).toFixed(token.precision);
        return (
          <div>{`${this.props.cmcPrice.legalPrefix}${availableTotal}`}</div>
        );
      } catch (error) {}
    }
    return <div>{Number(0).toFixed(token.precision)}</div>;
  };

  render() {
    const { balances, market, exchange } = this.props;
    const { prices } = this.props.cmcPrice;

    let baseTokenAvailableAmount;
    let baseTokenAvailableTotal;
    let quoteTokenAvailableAmount;
    let quoteTokenxAvailableTotal;

    if (
      balances &&
      balances.length &&
      prices &&
      prices.length &&
      exchange.tokens
    ) {
      const baseToken = config.getTokenBySymbol(
        market.currentMarket.baseTokenSymbol,
        exchange.tokens
      );
      baseTokenAvailableAmount = this.getAvailableAmount(baseToken, balances);
      baseTokenAvailableTotal = this.getAvailableTotalInFiat(
        baseToken,
        baseTokenAvailableAmount,
        prices,
        exchange.tokens
      );

      const quoteToken = config.getTokenBySymbol(
        market.currentMarket.quoteTokenSymbol,
        exchange.tokens
      );
      quoteTokenAvailableAmount = this.getAvailableAmount(quoteToken, balances);
      quoteTokenxAvailableTotal = this.getAvailableTotalInFiat(
        quoteToken,
        quoteTokenAvailableAmount,
        prices,
        exchange.tokens
      );
    } else {
      baseTokenAvailableAmount = <div>-</div>;
      baseTokenAvailableTotal = <div>&nbsp;</div>;
      quoteTokenAvailableAmount = <div>-</div>;
      quoteTokenxAvailableTotal = <div>&nbsp;</div>;
    }

    const header = (
      <div className="header">
        <Row className="row">
          <Col span={8}>
            <div
              className="columnLeft"
              style={{ userSelect: 'none', color: colors.textDim }}
            >
              <I s="Asset" />
            </div>
          </Col>
          <Col span={16}>
            <div
              className="columnRight"
              style={{ userSelect: 'none', color: colors.textDim }}
            >
              <I s="Available Balance" />
            </div>
          </Col>
        </Row>
      </div>
    );

    return (
      <div className="asset-panel">
        {header}
        <div className="body">
          <Row
            className="row"
            gutter={16}
            style={{
              color: colors.textWhite,
              fontSize: '0.9rem',
              paddingTop: '16px',
              paddingBottom: '0px'
            }}
          >
            <Col className="columnLeft" span={12} style={{}}>
              {this.props.market.currentMarket.baseTokenSymbol}
            </Col>
            <Col
              className="columnRight"
              span={12}
              style={{
                fontWeight: '600',
                fontSize: '0.9rem',
                color: colors.textWhite
              }}
            >
              {baseTokenAvailableAmount}
            </Col>
          </Row>
          <Row
            className="row"
            gutter={16}
            style={{
              color: colors.textWhite,
              paddingTop: '0px',
              paddingBottom: '0px'
            }}
          >
            <Col className="columnLeft" span={12}></Col>
            <Col
              className="columnRight"
              span={12}
              style={{
                fontWeight: '600',
                fontSize: '0.9rem',
                color: colors.textDim
              }}
            >
              {baseTokenAvailableTotal}
            </Col>
          </Row>
          <Row
            className="row"
            gutter={16}
            style={{
              color: colors.textWhite,
              fontSize: '0.9rem',
              paddingTop: '16px',
              paddingBottom: '0px'
            }}
          >
            <Col className="columnLeft" span={12} style={{}}>
              {this.props.market.currentMarket.quoteTokenSymbol}
            </Col>
            <Col
              className="columnRight"
              span={12}
              style={{
                fontSize: '0.9rem',
                fontWeight: '600',
                color: colors.textWhite
              }}
            >
              {quoteTokenAvailableAmount}
            </Col>
          </Row>
          <Row
            className="row"
            gutter={16}
            style={{
              color: colors.textWhite,
              fontSize: '0.9rem',
              paddingTop: '0px',
              paddingBottom: '16px'
            }}
          >
            <Col className="columnLeft" span={12}></Col>
            <Col
              className="columnRight"
              span={12}
              style={{
                fontWeight: '600',
                fontSize: '0.9rem',
                color: colors.textDim
              }}
            >
              {quoteTokenxAvailableTotal}
            </Col>
          </Row>
          <AssetPanelButtons />
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => {
  const { balances, market, cmcPrice, exchange } = state;
  return {
    balances: balances.balances,
    market,
    cmcPrice,
    exchange
  };
};

const mapDispatchToProps = dispatch => {
  return {};
};

export default connect(mapStateToProps, mapDispatchToProps)(AssetPanel);
