import React from 'react';
import colors from 'colors';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { faCircleNotch } from '@fortawesome/free-solid-svg-icons/faCircleNotch';
import {
  showDepositModal,
  showWithdrawModal
} from 'redux/actions/ModalManager';

import { DepositOutlineButton, WithdrawOutlineButton } from 'styles/Styles';
import { fetchWalletBalance } from 'modals/components/utils';
import I from 'components/I';
import SimpleTableWidget from 'components/SimpleTableWidget';

class BalanceTable extends React.PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      balanceOnEthereumDict: {}
    };
  }

  componentDidMount() {
    this.mounted = true;
    this.loadBalanceOnEthereum();
  }

  componentWillUnmount() {
    this.mounted = false;
  }

  componentDidUpdate(prevProps, prevState) {
    if (
      prevProps.balances.balances.length !== this.props.balances.balances.length
    ) {
      this.loadBalanceOnEthereum();
    }
  }

  // TODO: Have to use other API endpoints to get data
  loadBalanceOnEthereum() {
    (async () => {
      try {
        const { tokens } = this.props;
        const balanceOnEthereumDict = {};
        for (let i = 0; i < tokens.length; i++) {
          const token = tokens[i];
          balanceOnEthereumDict[token.symbol] = await fetchWalletBalance(
            this.props.dexAccount.account.address,
            token.symbol,
            tokens
          );
        }
        if (this.mounted) {
          this.setState({
            balanceOnEthereumDict
          });
        }
      } catch (error) {}
    })();
  }

  columnBuilders = [
    {
      label: 'Asset',
      align: 'left',
      width: 100,
      getColValue: balance => {
        return balance.token.symbol;
      }
    },
    {
      label: 'Asset Name',
      align: 'left',
      width: 200,
      getColValue: balance => {
        return <I s={balance.token.name} />;
      }
    },
    {
      label: 'Balance on Ethereum',
      align: 'right',
      getColValue: balance => {
        if (this.state.balanceOnEthereumDict[balance.token.symbol]) {
          return this.state.balanceOnEthereumDict[balance.token.symbol];
        } else {
          return (
            <div>
              <FontAwesomeIcon
                style={{
                  width: '14px',
                  height: '14px'
                }}
                color={colors.textDim}
                icon={faCircleNotch}
                spin={true}
              />
            </div>
          );
        }
      }
    },
    {
      label: 'Balance on Exchange',
      align: 'right',
      getColValue: balance => {
        return balance.totalAmountInString;
      }
    },
    {
      label: 'Available Balance',
      align: 'right',
      getColValue: balance => {
        return <div>{balance.available}</div>;
      }
    },
    {
      label: 'Operations',
      align: 'right',
      getColValue: balance => {
        return (
          <div>
            <DepositOutlineButton
              style={{
                marginRight: '8px'
              }}
              onClick={() => {
                this.props.showDepositModal(true, balance.token.symbol);
              }}
            >
              <I s="Deposit" />
            </DepositOutlineButton>
            <WithdrawOutlineButton
              onClick={() => {
                this.props.showWithdrawModal(true, balance.token.symbol);
              }}
            >
              <I s="Withdraw" />
            </WithdrawOutlineButton>
          </div>
        );
      }
    }
  ];

  render() {
    let { balances } = this.props.balances;
    const tokens = this.props.tokens.filter(token => {
      return token.symbol !== 'WETH';
    });
    balances = balances || [];
    balances = tokens.map(token => {
      const balance = balances.find(ba => ba.token.tokenId === token.tokenId);
      if (balance) {
        return balance;
      } else {
        return {
          token,
          totalAmountInString: Number(0).toFixed(token.precision),
          available: Number(0).toFixed(token.precision)
        };
      }
    });
    return (
      <SimpleTableWidget
        margin={20}
        emptyText="NoBalance"
        columnBuilders={this.columnBuilders}
        rowData={balances}
      />
    );
  }
}

const mapStateToProps = state => {
  const { dexAccount, balances, exchange } = state;
  return {
    dexAccount,
    balances,
    tokens: exchange.tokens
  };
};

const mapDispatchToProps = dispatch => {
  return bindActionCreators(
    {
      showDepositModal,
      showWithdrawModal
    },
    dispatch
  );
};

export default connect(mapStateToProps, mapDispatchToProps)(BalanceTable);
