import {
  CANCELED_ORDERS,
  REMOVE_CANCELED_ORDERS
} from 'redux/actions/CancelOrderManager';

import {
  removeCanceledOrdersByAddressFromLocal,
  saveCanceledOrderToLocal
} from 'lightcone/api/localStorgeAPI';

const initialState = {
  canceledOrders: []
};

export const CancelOrderManagerReducer = (state = initialState, action) => {
  switch (action.type) {
    case CANCELED_ORDERS:
      saveCanceledOrderToLocal(state.account.address, action.payload.hash);
      return {
        ...state,
        canceledOrders: [...state.canceledOrders, action.payload.hash]
      };

    case REMOVE_CANCELED_ORDERS:
      removeCanceledOrdersByAddressFromLocal(state.account.address);
      return {
        ...state,
        canceledOrders: []
      };
    default:
      return state;
  }
};
