import { ActionButton } from 'styles/Styles';
import { Form, Input } from 'antd';
import { MyModal } from './styles/Styles';
import { connect } from 'react-redux';
import {
  showEnterPasswordModal,
  showExportAccountModal
} from 'redux/actions/ModalManager';
import { updatePassword } from 'redux/actions/DexAccount';
import AppLayout from 'AppLayout';
import I from 'components/I';
import React from 'react';
import colors from 'colors';

import { decrypt } from 'modals/components/utils';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

import { faTimes } from '@fortawesome/free-solid-svg-icons/faTimes';

import { utils } from 'lightcone/common';

class EnterPasswordModal extends React.Component {
  passwordRef = React.createRef();

  state = {
    loading: false,
    password: '',
    hidePasswordErrorMessage: true,
    isPasswordValid: false,
    passwordErrorMessage: <I s="Please enter your password!" />
  };

  componentDidUpdate(prevProps, prevState) {
    if (
      this.props.modalManager.isEnterPasswordModalVisible === false &&
      this.props.modalManager.isEnterPasswordModalVisible !==
        prevProps.modalManager.isEnterPasswordModalVisible
    ) {
      this.setState({
        password: '',
        loading: false,
        hidePasswordErrorMessage: true,
        passwordErrorMessage: <I s="Please enter your password!" />
      });
    }

    if (this.passwordRef.current) {
      this.passwordRef.current.focus();
    }
  }

  onPasswordChange = e => {
    const { value } = e.target;
    const isPasswordValid = value.length >= 12 && value.length <= 128;
    this.setState({
      password: value,
      hidePasswordErrorMessage: true,
      isPasswordValid
    });
  };

  pressedButton = () => {
    let validate = true;
    if (this.state.password.length === 0) {
      validate = false;
      this.setState({
        hidePasswordErrorMessage: false,
        passwordErrorMessage: <I s="Please enter your password!" />
      });
    } else {
      // Use password to decode
      const keyPairCipherText = this.props.dexAccount.account.keyPairCipherText;
      const tradingPrivKey = decrypt(
        keyPairCipherText,
        utils.keccakHash(this.state.password)
      );
      if (tradingPrivKey === null) {
        validate = false;
        this.setState({
          hidePasswordErrorMessage: false,
          passwordErrorMessage: <I s="InvalidPassword" />
        });
      } else {
        // Password is correct
        this.props.showExportAccountModal(true);
      }
    }

    if (validate) {
      this.props.updatePassword(this.state.password);
      // Reset password
      this.setState({
        password: '',
        hidePasswordErrorMessage: true
      });

      this.props.showEnterPasswordModal(false);
    }
  };

  passwordEnter = e => {
    if (e.keyCode === 13) {
      e.preventDefault();
      this.pressedButton();
    }
  };

  render() {
    let address = this.props.dexAccount.account.address;
    const container = (
      <div>
        <div>
          <span
            style={{
              color: colors.textWhite,
              fontSize: '0.9rem'
            }}
          >
            <I s="PromptForPassword" />
          </span>
        </div>
        <div
          style={{
            marginBottom: '0px' // distance between components
          }}
        >
          <div>
            <div
              style={{
                paddingTop: '24px',
                paddingBottom: '9px',
                paddingLeft: '0px',
                fontSize: '0.9rem'
              }}
            >
              <I s="My Address" />
            </div>
            <div
              style={{
                padding: '10px 16px',
                backgroundColor: colors.foreground,
                color: colors.textDim,
                borderRadius: '4px',
                fontSize: '0.9rem'
              }}
            >
              {address}
            </div>
          </div>
          <Form>
            <div
              style={{
                paddingTop: '32px',
                paddingBottom: '9px',
                paddingLeft: '0px',
                fontSize: '0.9rem'
              }}
            >
              <I s="Password" />
            </div>
            <Form.Item
              style={{
                paddingTop: '0px',
                paddingBottom: '0px'
              }}
              colon={false}
              hasFeedback
            >
              <Input.Password
                autoFocus
                autoCapitalize={'off'}
                autoComplete={'off'}
                value={this.state.password}
                onChange={this.onPasswordChange}
                onBlur={this.onBlur}
                placeholder=""
                maxLength={128}
                style={{
                  color: colors.textWhite,
                  fontWeight: '600',
                  fontSize: '0.9rem'
                }}
                onKeyDown={this.passwordEnter.bind(this)}
                ref={this.passwordRef}
              />
            </Form.Item>
          </Form>
          <div
            style={{
              height: '2px',
              marginTop: '6px',
              marginBottom: '6px'
            }}
          >
            <span
              style={{
                color: colors.red,
                fontSize: '0.9rem'
              }}
            >
              {this.state.hidePasswordErrorMessage
                ? ''
                : this.state.passwordErrorMessage}
            </span>
          </div>
        </div>
        <div
          style={{
            paddingTop: '40px',
            paddingBottom: '2px'
          }}
        >
          <ActionButton
            block
            // className={'nav-tab-theme-button'}
            onClick={() => this.pressedButton()}
            disabled={
              this.state.password.length === 0 || !this.state.isPasswordValid
            }
          >
            <I s="Export" />
          </ActionButton>
        </div>
      </div>
    );

    return (
      <MyModal
        width={AppLayout.modalWidth}
        title={
          <span
            style={{
              color: colors.textWhite,
              fontWeight: '600',
              fontSize: '1.2rem',
              userSelect: 'none'
            }}
          >
            <I s="Export Account" />
          </span>
        }
        footer={null}
        maskClosable={false}
        closeIcon={<FontAwesomeIcon icon={faTimes} />}
        visible={this.props.modalManager.isEnterPasswordModalVisible}
        onOk={() => this.props.showEnterPasswordModal(false)}
        onCancel={() => this.props.showEnterPasswordModal(false)}
      >
        {container}
      </MyModal>
    );
  }
}

const mapStateToProps = state => {
  const { dexAccount, modalManager } = state;
  return { dexAccount, modalManager };
};

const mapDispatchToProps = dispatch => {
  return {
    showEnterPasswordModal: show => dispatch(showEnterPasswordModal(show)),
    showExportAccountModal: show => dispatch(showExportAccountModal(show)),
    updatePassword: password => dispatch(updatePassword(password))
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(EnterPasswordModal);
