import styled from 'styled-components';

// TODO(ruby): this should be a React component, not a styled object
const OrderSizeBar = styled.td`
  {
    padding: ${props => {
      const totalUnits = props.sizeBarMaxSize / props.sizeBarUnitSize;
      const numUnits = Math.floor(props.size / props.sizeBarUnitSize);
      const percentSize = numUnits >= totalUnits ? 1 : numUnits / totalUnits;
      const sizeBarWidth = (1 - percentSize) * props.sizeBarMaxWidth + 1;

      return '0 0 0 ' + sizeBarWidth + 'px';
    }};
    boxs-sizing: border-box;
    border-color: ${props =>
      props.showSizeBar
        ? props.side === 'buy'
          ? props.buyBar
          : props.sellBar
        : 'red'};
    border-style: solid;
    border-width: ${props => {
      const totalUnits = props.sizeBarMaxSize / props.sizeBarUnitSize;
      const numUnits = Math.floor(props.size / props.sizeBarUnitSize);
      const percentSize = numUnits >= totalUnits ? 1 : numUnits / totalUnits;
      const sizeBarWidth = percentSize * props.sizeBarMaxWidth + 1;

      return '0 0 0 ' + sizeBarWidth + 'px';
    }};

`;

export default OrderSizeBar;
