import { connect } from 'react-redux';
import I from 'components/I';
import React from 'react';

import colors from 'colors';

import { ConfigProvider, Pagination, Table } from 'antd';
import Moment from 'moment';

import {
  LargeTableContainer,
  LargeTableRow,
  TextCompactTableHeader
} from 'styles/Styles';
import EmptyTableIndicator from 'components/EmptyTableIndicator';

import { compareDexAccounts } from 'components/services/utils';
import { fetchUserTransactions } from 'redux/actions/MyOrderPage';

import { LOGGED_IN } from 'redux/actions/DexAccount';

import 'pages/account/components/BalanceTable.less';

class HistoryTradesTable extends React.Component {
  componentDidMount() {
    try {
      this.loadData();
    } catch (error) {}
  }

  componentDidUpdate(prevProps, prevState) {
    if (
      (this.props.exchange.isInitialized !== prevProps.exchange.isInitialized ||
        !compareDexAccounts(prevProps.dexAccount, this.props.dexAccount)) &&
      this.props.dexAccount.account.apiKey
    ) {
      this.loadData();
    }
  }

  loadData(offset = -1) {
    const {
      dexAccount,
      exchange,
      myOrderPage,
      fetchUserTransactions
    } = this.props;

    if (
      exchange.isInitialized &&
      !!dexAccount.account.accountId &&
      dexAccount.account.state === LOGGED_IN &&
      dexAccount.account.apiKey
    ) {
      fetchUserTransactions(
        dexAccount.account.accountId,
        myOrderPage.transactionsLimit,
        offset !== -1 ? offset : myOrderPage.transactionsOffset,
        dexAccount.account.apiKey,
        exchange.tokens
      );
    }
  }

  onChange = page => {
    const offset = this.props.myOrderPage.transactionsLimit * (page - 1);
    this.loadData(offset);
  };

  render() {
    const current =
      Math.floor(
        this.props.myOrderPage.transactionsOffset /
          this.props.myOrderPage.transactionsLimit
      ) + 1;

    const customizeRenderEmpty = () => (
      <EmptyTableIndicator text={'NoHistoryOrders'} />
    );

    const columns = [
      {
        title: '',
        dataIndex: 'padding',
        width: 14
      },
      {
        title: (
          <TextCompactTableHeader>
            <I s="Side" />
          </TextCompactTableHeader>
        ),
        dataIndex: 'side'
      },
      {
        title: (
          <TextCompactTableHeader>
            <I s="Market" />
          </TextCompactTableHeader>
        ),
        dataIndex: 'market'
      },
      {
        title: (
          <TextCompactTableHeader>
            <I s="Filled Size" />
          </TextCompactTableHeader>
        ),
        dataIndex: 'size'
      },
      {
        title: (
          <TextCompactTableHeader>
            <I s="Actual Settlement Price" />
          </TextCompactTableHeader>
        ),
        dataIndex: 'price'
      },
      {
        title: (
          <TextCompactTableHeader>
            <I s="Fee" />
          </TextCompactTableHeader>
        ),
        dataIndex: 'fee'
      },
      {
        title: (
          <TextCompactTableHeader
            style={{
              paddingRight: '14px'
            }}
          >
            <I s="Timestamp" />
          </TextCompactTableHeader>
        ),
        dataIndex: 'date',
        width: 180
      }
    ];

    const data = [];
    for (let i = 0; i < this.props.myOrderPage.transactions.length; i++) {
      const order = this.props.myOrderPage.transactions[i];

      data.push({
        key: i,
        side: (
          <LargeTableRow
            style={{
              color:
                order.side === 'BUY'
                  ? this.props.colorManager.buyPrimary
                  : this.props.colorManager.sellPrimary
            }}
          >
            {order.side === 'BUY' ? <I s="Buy" /> : <I s="Sell" />}
          </LargeTableRow>
        ),
        market: <LargeTableRow>{order.market}</LargeTableRow>,
        size: (
          <LargeTableRow>
            {order.sizeInString} {order.market.split('-')[0]}
          </LargeTableRow>
        ),
        price: (
          <LargeTableRow
            style={{
              color:
                order.side === 'BUY'
                  ? this.props.colorManager.buyPrimary
                  : this.props.colorManager.sellPrimary
            }}
          >
            {Number(order.price)} {order.market.split('-')[1]}
          </LargeTableRow>
        ),
        fee: (
          <LargeTableRow
            style={{
              color: colors.textDim
            }}
          >
            {order.feeInString}
            {order.side === 'BUY'
              ? order.market.split('-')[0]
              : order.market.split('-')[1]}
          </LargeTableRow>
        ),
        date: (
          <LargeTableRow
            style={{
              color: colors.textDim,
              textAlign: 'right',
              paddingRight: '14px'
            }}
          >
            {Moment(order.timestamp).format('YYYY-MM-DD HH:mm:ss')}
          </LargeTableRow>
        )
      });
    }

    const hasPagination =
      this.props.myOrderPage.transactionsTotalNum >
      this.props.myOrderPage.transactionsLimit;

    return (
      <LargeTableContainer className={'my-table'}>
        <ConfigProvider renderEmpty={data.length === 0 && customizeRenderEmpty}>
          <Table
            style={{
              borderStyle: 'none',
              borderWidth: '0px',
              height: `${this.props.myOrderPage.transactions.length * 34 +
                35}px`,
              minHeight: '500px'
            }}
            size="middle"
            columns={columns}
            dataSource={data}
            pagination={false}
            scroll={{
              y: `${this.props.myOrderPage.transactions.length * 34}px`
            }}
          />
          {hasPagination ? (
            <Pagination
              style={{
                padding: '30px 0px 30px 0px',
                background: colors.background,
                textAlign: 'center'
              }}
              size=""
              total={this.props.myOrderPage.transactionsTotalNum}
              current={current}
              onChange={this.onChange}
              pageSize={this.props.myOrderPage.transactionsLimit}
            />
          ) : (
            <div />
          )}
        </ConfigProvider>
      </LargeTableContainer>
    );
  }
}

const mapStateToProps = state => {
  const { colorManager, dexAccount, myOrderPage, exchange } = state;
  return { colorManager, dexAccount, myOrderPage, exchange };
};

const mapDispatchToProps = dispatch => {
  return {
    fetchUserTransactions: (accountId, limit, offset, apiKey, tokens) =>
      dispatch(fetchUserTransactions(accountId, limit, offset, apiKey, tokens))
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(HistoryTradesTable);
