import { ActionButton } from 'styles/Styles';
import {
  AddressDiv,
  Section,
  TextFormError,
  TextPopupTitle
} from 'modals/styles/Styles';
import { Input, Spin } from 'antd';
import {
  LOGGED_IN,
  RESETTING_PASSWORD,
  updateAccount,
  updatePassword,
  updateRememberMe
} from 'redux/actions/DexAccount';
import { MyModal } from './styles/Styles';
import { connect } from 'react-redux';
import { lightconeGetAccount } from 'lightcone/api/LightconeAPI';
import { loginModal, resetPasswordModal } from 'redux/actions/ModalManager';
import AppLayout from 'AppLayout';
import Group from 'modals/components/Group';
import I from 'components/I';
import ModalIndicator from 'modals/components/ModalIndicator';
import React from 'react';
import colors from 'colors';

import { encrypt } from 'modals/components/utils';
import { utils } from 'lightcone/common';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

import { faTimes } from '@fortawesome/free-solid-svg-icons/faTimes';

class LoginModal extends React.Component {
  passwordRef = React.createRef();

  state = {
    password: '',
    error: '',
    loading: false
  };

  componentDidUpdate(prevProps, prevState) {
    if (
      this.props.isVisible === false &&
      this.props.isVisible !== prevProps.isVisible
    ) {
      this.setState({
        password: '',
        error: ''
      });
    }

    if (this.passwordRef.current) {
      this.passwordRef.current.focus();
    }
  }

  onPasswordChange = e => {
    const { value } = e.target;
    this.setState({
      password: value,
      error: '',
      loading: false
    });
  };

  onClose = () => {
    this.props.closeModal();
  };

  onClick = () => {
    this.setState({
      loading: true
    });

    const _this = this;
    const { updateRememberMe, updateAccount } = this.props;
    const { account } = this.props.dexAccount;

    (async () => {
      try {
        const passwordHash = utils.keccakHash(_this.state.password);
        const keyPair = window.wallet.generateKeyPair(_this.state.password);
        if (
          !!keyPair.secretKey &&
          keyPair.publicKeyX === account.publicKeyX &&
          keyPair.publicKeyY === account.publicKeyY
        ) {
          const relayAccount = await lightconeGetAccount(window.wallet.address);
          const keyPairCipherText = encrypt(keyPair.secretKey, passwordHash);
          const state = relayAccount.isFreeze ? RESETTING_PASSWORD : LOGGED_IN;
          updateAccount({
            ...account,
            state,
            keyPairCipherText,
            password: passwordHash
          });

          updateRememberMe(true);

          _this.onClose();
          _this.setState({
            loading: false
          });
          // notifySuccess(<I s="WelcomeBack" />);
        } else {
          throw new Error('Invalid password');
        }
      } catch (err) {
        _this.setState({
          password: '',
          loading: false,
          error: <I s="InvalidPassword" />
        });
      }
    })();
  };

  onPasswordEnter = e => {
    if (e.keyCode === 13) {
      e.preventDefault();
      this.onClick();
    }
  };

  render() {
    return (
      <MyModal
        width={AppLayout.modalWidth}
        title={
          <TextPopupTitle>
            <I s="Login" />
          </TextPopupTitle>
        }
        footer={null}
        maskClosable={false}
        closeIcon={<FontAwesomeIcon icon={faTimes} />}
        visible={this.props.isVisible}
        onCancel={() => this.onClose()}
      >
        <Spin
          spinning={this.state.loading}
          indicator={<ModalIndicator title="Logging in..." marginTop="80px" />}
        >
          <Section>
            <Group label={<I s="My Address" />}>
              <AddressDiv>{this.props.dexAccount.account.address}</AddressDiv>
            </Group>

            <Group label={<I s="Password" />}>
              <Input.Password
                autoFocus
                autoCapitalize={'off'}
                autoComplete={'off'}
                value={this.state.password}
                onChange={this.onPasswordChange}
                placeholder=""
                maxLength={128}
                style={{
                  color: colors.textWhite
                }}
                onKeyDown={this.onPasswordEnter.bind(this)}
                ref={this.passwordRef}
              />
              <div
                style={{
                  marginTop: '6px',
                  height: '20px'
                }}
              >
                <TextFormError>{this.state.error}</TextFormError>
              </div>
            </Group>
          </Section>
          <Section>
            <Group>
              <a
                href
                onClick={() => {
                  this.props.closeModal();
                  this.props.resetPasswordModal(true);
                }}
                style={{
                  display: 'inline-block',
                  // marginBottom: '24px',
                  // marginTop: '-12px',
                  float: 'right'
                }}
              >
                <I s="Forgot password?" />
              </a>
            </Group>
          </Section>
          <Section>
            <ActionButton
              disabled={!this.state.password}
              onClick={() => this.onClick()}
            >
              <I s="Login" />
            </ActionButton>
          </Section>
        </Spin>
      </MyModal>
    );
  }
}

const mapStateToProps = state => {
  const { modalManager, dexAccount } = state;
  const isVisible = modalManager.isLoginModalVisible;
  return { isVisible, modalManager, dexAccount };
};

const mapDispatchToProps = dispatch => {
  return {
    closeModal: () => dispatch(loginModal(false)),
    resetPasswordModal: show => dispatch(resetPasswordModal(show)),
    updateAccount: account => dispatch(updateAccount(account)),
    updatePassword: password => dispatch(updatePassword(password)),
    updateRememberMe: rememberMe => dispatch(updateRememberMe(rememberMe))
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(LoginModal);
