import { connect } from 'react-redux';
import { formatter } from 'lightcone/common';
import { toBig } from 'lightcone/common/formatter';
import {
  updateAmount,
  updatePrice,
  updateTradeType
} from 'redux/actions/TradePanel';
import OrderBook from 'pages/trade/orderbook/OrderBook';
import React from 'react';
import TabHeader from './components/TabHeader';
import TradeHistory from 'pages/trade/trade-history/TradeHistory';
import colors from 'colors';
import config from 'lightcone/config';

import AggregationWidget from './components/AggregationWidget';

import './OrderBookTradeHistoryPanel.less';

class OrderBookTradeHistoryPanel extends React.Component {
  render() {
    let sizeBarMaxSize;

    const { market, exchange } = this.props;
    const baseTokenSymbol = this.props.market.currentMarket.baseTokenSymbol;
    const quoteTokenSymbol = this.props.market.currentMarket.quoteTokenSymbol;
    const baseToken = config.getTokenBySymbol(baseTokenSymbol, exchange.tokens);
    const marketConfig = config.getMarketByPair(
      market.currentMarket.current,
      exchange.markets
    ) || {
      precisionForPrice: 6
    };

    const sizeFormat = baseToken.precision
      ? '0.' + '0'.repeat(baseToken.precision)
      : '1' + '0'.repeat(-baseToken.precision);
    const priceFormat =
      marketConfig.precisionForPrice > 0
        ? ' 0.' + '0'.repeat(marketConfig.precisionForPrice)
        : '1' + '0'.repeat(-marketConfig.precisionForPrice);

    var sizeMaxInSell = Math.max.apply(
      Math,
      this.props.market.orderBook.sells.map(function(o) {
        return o.sizeInNumber;
      })
    );

    var sizeMaxInBuy = Math.max.apply(
      Math,
      this.props.market.orderBook.buys.map(function(o) {
        return o.sizeInNumber;
      })
    );

    sizeBarMaxSize = Math.max(sizeMaxInSell, sizeMaxInBuy);
    if (!isFinite(sizeBarMaxSize)) {
      sizeBarMaxSize = 1000;
    }

    const orderbookSide = (
      <div className="orderbook-side">
        <OrderBook
          hideHeader={false}
          depth={30}
          baseTokenSymbol={baseTokenSymbol}
          quoteTokenSymbol={quoteTokenSymbol}
          asks={this.props.market.orderBook.sells}
          bids={this.props.market.orderBook.buys}
          sizeBarMaxSize={sizeBarMaxSize}
          latestTrade={this.props.market.tradeHistory.latestTrade}
          getPrice={entry => {
            // Have to check if entry is null or not
            if (entry) {
              return entry.price;
            } else {
              return 0;
            }
          }}
          getSize={entry => {
            try {
              return entry.sizeInNumber;
            } catch (error) {
              return 0;
            }
          }}
          getPosition={entry => {
            const { openOrders } = this.props.myOrders;
            var totalSize = 0;
            for (var i = 0; i < openOrders.length; i = i + 1) {
              const myOrder = openOrders[i];

              const orderPrice =
                myOrder['side'].toUpperCase() === 'SELL'
                  ? toBig(myOrder['price']).toFixed(
                      marketConfig.precisionForPrice -
                        this.props.market.orderBook.level,
                      2
                    )
                  : toBig(myOrder['price']).toFixed(
                      marketConfig.precisionForPrice -
                        this.props.market.orderBook.level,
                      3
                    );

              if (
                toBig(orderPrice).eq(entry['price']) &&
                myOrder['side'] === entry['side'] &&
                myOrder['status'] === 'processing'
              ) {
                try {
                  const sizeInBigNumber = myOrder['size'];
                  const filledInBigNumber = myOrder['filledSize'];
                  const size = Number(
                    config.fromWEI(
                      this.props.market.currentMarket.baseTokenSymbol,
                      sizeInBigNumber,
                      exchange.tokens
                    )
                  );
                  const filledSize = Number(
                    config.fromWEI(
                      this.props.market.currentMarket.baseTokenSymbol,
                      filledInBigNumber,
                      exchange.tokens
                    )
                  );
                  totalSize = totalSize + size - filledSize;
                } catch (error) {}
              }
            }
            if (totalSize > 0) {
              return totalSize;
            } else {
              return 'null';
            }
          }}
          onClickOrder={(order, side) => {
            this.props.updatePrice(toBig(order.price).toFixed());
            let tradeSide = 'buy';
            let size = 0;
            if (side.toLowerCase() === 'buy') {
              const slots = this.props.market.orderBook.buys.filter(
                slot => parseFloat(slot.price) >= parseFloat(order.price)
              );
              size = slots
                .map(slot =>
                  formatter.toBig(
                    config.fromWEI(
                      this.props.market.currentMarket.baseTokenSymbol,
                      slot.size,
                      exchange.tokens
                    )
                  )
                )
                .reduce((sum, size) => sum.plus(size));
              tradeSide = 'sell';
            } else {
              const slots = this.props.market.orderBook.sells.filter(
                slot => parseFloat(slot.price) <= parseFloat(order.price)
              );
              size = slots
                .map(slot =>
                  formatter.toBig(
                    config.fromWEI(
                      this.props.market.currentMarket.baseTokenSymbol,
                      slot.size,
                      exchange.tokens
                    )
                  )
                )
                .reduce((sum, size) => sum.plus(size));
            }
            this.props.updateTradeType(tradeSide);
            this.props.updateAmount(formatter.toNumber(size));
          }}
        />
        <AggregationWidget />
      </div>
    );

    let content = (
      <div
        style={{
          display: this.props.tabs.type1 === 'orderBook' ? 'initial' : 'none'
        }}
      >
        <OrderBook
          style={{
            height: 'calc(100vh - 64px - 45px - 45.59px + 8px)'
          }}
          hideHeader={true}
          baseTokenSymbol={baseTokenSymbol}
          quoteTokenSymbol={quoteTokenSymbol}
          depth={1000}
          asks={this.props.market.orderBook.sells}
          bids={this.props.market.orderBook.buys}
          sizeBarMaxSize={sizeBarMaxSize}
          latestTrade={this.props.market.tradeHistory.latestTrade}
          getPrice={entry => {
            // Have to check if entry is null or not
            if (entry) {
              return entry.price;
            } else {
              return 0;
            }
          }}
          getSize={entry => {
            try {
              return config.fromWEI(
                baseTokenSymbol,
                entry.size,
                exchange.tokens
              );
            } catch (error) {
              return 0;
            }
          }}
          getPosition={entry => {
            const { openOrders } = this.props.myOrders;
            var totalSize = 0;
            for (var i = 0; i < openOrders.length; i = i + 1) {
              const myOrder = openOrders[i];
              const orderPrice =
                myOrder['side'].toUpperCase() === 'SELL'
                  ? toBig(myOrder['price']).toFixed(
                      marketConfig.precisionForPrice -
                        this.props.market.orderBook.level,
                      2
                    )
                  : toBig(myOrder['price']).toFixed(
                      marketConfig.precisionForPrice -
                        this.props.market.orderBook.level,
                      3
                    );

              if (
                toBig(orderPrice).eq(entry['price']) &&
                myOrder['side'] === entry['side'] &&
                myOrder['status'] === 'processing'
              ) {
                try {
                  const sizeInBigNumber = myOrder['size'];
                  const filledInBigNumber = myOrder['filledSize'];
                  const size = Number(
                    config.fromWEI(
                      this.props.market.currentMarket.baseTokenSymbol,
                      sizeInBigNumber,
                      exchange.tokens
                    )
                  );
                  const filledSize = Number(
                    config.fromWEI(
                      this.props.market.currentMarket.baseTokenSymbol,
                      filledInBigNumber,
                      exchange.tokens
                    )
                  );
                  totalSize = totalSize + size - filledSize;
                } catch (error) {}
              }
            }
            if (totalSize > 0) {
              return totalSize;
            } else {
              return 'null';
            }
          }}
          onClickOrder={(order, side) => {
            this.props.updatePrice(toBig(order.price).toFixed());
            let tradeSide = 'buy';
            let size = 0;
            if (side.toLowerCase() === 'buy') {
              const slots = this.props.market.orderBook.buys.filter(
                slot => parseFloat(slot.price) >= parseFloat(order.price)
              );
              size = slots
                .map(slot =>
                  formatter.toBig(
                    config.fromWEI(
                      this.props.market.currentMarket.baseTokenSymbol,
                      slot.size,
                      exchange.tokens
                    )
                  )
                )
                .reduce((sum, size) => sum.plus(size));
              tradeSide = 'sell';
            } else {
              const slots = this.props.market.orderBook.sells.filter(
                slot => parseFloat(slot.price) <= parseFloat(order.price)
              );
              size = slots
                .map(slot =>
                  formatter.toBig(
                    config.fromWEI(
                      this.props.market.currentMarket.baseTokenSymbol,
                      slot.size,
                      exchange.tokens
                    )
                  )
                )
                .reduce((sum, size) => sum.plus(size));
            }
            this.props.updateTradeType(tradeSide);
            this.props.updateAmount(formatter.toNumber(size));
          }}
        />
        <AggregationWidget />
      </div>
    );
    const orderBookAndTradeHistorySide = (
      <div className="orderbook-side-and-trade-history">
        <div
          style={{
            height: '44px',
            backgroundColor: colors.background
          }}
        >
          <TabHeader />
        </div>
        {content}
        <TradeHistory
          style={{
            display: this.props.tabs.type1 === 'orderBook' ? 'none' : 'initial',
            height: 'calc(100vh - 64px - 45px - 45.59px + 8px)'
          }}
          hideHeader={true}
          getTimeStamp={order => order.timestamp}
          onClickTrade={(order, side) => {
            this.props.updatePrice(toBig(order.price).toFixed());
          }}
          sizeFormat={sizeFormat}
          priceFormat={priceFormat}
        />
      </div>
    );

    return (
      <div>
        {orderbookSide}
        {orderBookAndTradeHistorySide}
      </div>
    );
  }
}

const mapStateToProps = state => {
  const { layoutManager, market, tabs, myOrders, exchange } = state;
  return { layoutManager, market, tabs, myOrders, exchange };
};

const mapDispatchToProps = dispatch => {
  return {
    updatePrice: price => dispatch(updatePrice(price)),
    updateAmount: size => dispatch(updateAmount(size)),
    updateTradeType: type => dispatch(updateTradeType(type))
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(OrderBookTradeHistoryPanel);
