import { Layout } from 'antd';
import { Scroller } from 'styles/Styles';
import { connect } from 'react-redux';
import { getLanguage } from 'lightcone/api/localStorgeAPI';
import AppLayout from 'AppLayout';

import React from 'react';
import ReactMarkdown from 'react-markdown/with-html';
import colors from 'colors';
import styled from 'styled-components';
const { Content, Sider } = Layout;

const MarkdownWrapper = styled.div`
  padding: 64px 40px 0  40px;
  font-family: Montserrat, sans-serif;
  color: ${colors.textWhiteBulk};
  font-size: 1rem;

  h1,
  h2,
  h3,
  h4 {
    font-weight: 600;
    color: ${colors.textWhite};
  }
  h1 {
    padding-top: 20px;
    font-size: 36px;
    text-transform: uppercase;
    margin-bottom: 2rem;
    color: ${colors.textBright}
  }
  h2 {
    text-transform: uppercase;
    font-size: 26px;
    margin-top: 2.4rem;
    color: ${colors.theme}
  }
  h3 {
    margin-top: 2rem;
    font-size: 18px;
    color: ${colors.textWhite}
  }

  blockquote {
    color: ${colors.textDim}
    font-style: italic;
    border-left: 2px solid ${colors.textDim}
    padding: 4px 16px 4px 16px;
  }

  mark {
    background: transparent;
    color: ${colors.green}
  }

  pre {
    font-family: monospace !important;
    color: ${colors.orange};
    background: rgba(0, 0, 0, 0.1);
    border-radius: 4px;
    font-size: 0.85rem;
    padding: 16px 8px;
  }

  hr {
    margin: 16px 0;
    border-color: ${colors.sidePanelBackground};
  }

  img {
    max-width: 640px;
  }

  table {
    background: ${colors.foreground}
    margin: 24px auto;
    font-size: 0.9rem;
    min-width: 100%;
  }

  thead {
    background: linear-gradient(
        180deg,
        rgba(46, 47, 74, 1) 0%,
        rgba(37, 38, 59, 1) 100%
      );

  }

  thead > tr> th {
     color: ${colors.textDim};
     font-weight: normal;
  }

  tbody {
    min-width: 80%;
    border: 1px solid ${colors.border}
  }

  td, th {
    padding: 8px 12px;
  }

  tr {
    border-bottom: 1px solid ${colors.border}
  }

  tbody> tr:hover  > td{
    color: ${colors.textBright}
    background: ${colors.lightForeground};
  }

  td {
    color: ${colors.textWhite}
  }

  ul, ol {
    padding-inline-start: 18px;
  }
`;

class MakrdownPage extends React.Component {
  state = {
    markdown: '',
    loading: true
  };

  componentDidMount() {
    const url = `/assets/markdown/${getLanguage()}/${this.props.fileName}`;
    fetch(url)
      .then(r => r.text())
      .then(text => {
        this.setState({
          markdown: text,
          loading: false
        });
      });
  }

  getContainerBackground() {
    return this.state.loading
      ? 'url(/assets/images/bars.svg) center center no-repeat'
      : 'none';
  }

  render() {
    return (
      <div>
        <Layout
          hasSider
          style={{
            height: AppLayout.simpleSecondaryPageHeight
          }}
        >
          <Sider
            width={AppLayout.tradePanelWidth}
            style={{
              borderTop: '1px solid ' + colors.foreground,
              padding: '0px',
              background: colors.sidePanelBackground,
              borderStyle: 'none'
            }}
            trigger={null}
            breakpoint="sm"
            collapsedWidth="0"
            onBreakpoint={broken => {}}
            onCollapse={(collapsed, type) => {
              this.props.updateColumns();
            }}
          >
            <Scroller
              style={{
                borderTop: '1px solid ' + colors.foreground
              }}
            >
              {this.props.navigation}
            </Scroller>
          </Sider>
          <Content
            width="100%"
            style={{
              backgroundColor: colors.background,
              borderLeftStyle: 'none'
            }}
          >
            <div
              style={{
                marginLeft: 'auto',
                marginRight: 'auto',
                width: '740px',
                overflow: 'scroll',
                height: '100%',
                background: this.getContainerBackground(),
                backgroundSize: '40px 40px'
              }}
            >
              <MarkdownWrapper>
                <ReactMarkdown
                  escapeHtml={false}
                  source={this.state.markdown}
                />
              </MarkdownWrapper>
            </div>
          </Content>
        </Layout>
      </div>
    );
  }
}

const mapStateToProps = state => {
  const { layoutManager } = state;
  return { layoutManager };
};

export default connect(mapStateToProps, null)(MakrdownPage);
