import { getMarketInfo } from 'lightcone/api/v1/marketinfo';

export const UPDATE_MARKET_LIST = 'UPDATE_MARKET_LIST';
export const FETCH_MARKET_LIST = 'FETCH_MARKET_LIST';

export function updateMarketList(markets) {
  return {
    type: UPDATE_MARKET_LIST,
    payload: {
      markets
    }
  };
}

export function fetchMarketList() {
  return dispatch => {
    (async () => {
      try {
        const markets = await getMarketInfo();
        dispatch(updateMarketList(markets));
      } catch (error) {}
    })();
  };
}
