import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { notification } from 'antd';
import { tracker } from 'components/DefaultTracker';
import React from 'react';

import { faCheckCircle } from '@fortawesome/free-solid-svg-icons/faCheckCircle';
import { faGhost } from '@fortawesome/free-solid-svg-icons/faGhost';
import { faInfoCircle } from '@fortawesome/free-solid-svg-icons/faInfoCircle';
import { faTimes } from '@fortawesome/free-solid-svg-icons/faTimes';
import colors from 'colors';

const topPosition = 56;
const backgroundColor = '#3d3e59';

const closeIcon = (
  <FontAwesomeIcon style={{ color: colors.textDim }} icon={faTimes} />
);
function trackEvent(message) {
  try {
    tracker.trackEvent({
      type: 'USER_NOTIFICATION',
      data: {
        message: message['props']['s']
      }
    });
  } catch (error) {}
}

export function notifySuccess(message, duration) {
  trackEvent(message);
  notification.success({
    message: message,
    closeIcon: closeIcon,
    icon: <FontAwesomeIcon icon={faCheckCircle} />,
    duration: duration || 3,
    top: topPosition,
    style: {
      color: colors.green,
      background: backgroundColor
    }
  });
}

export function notifyWarning(message) {
  trackEvent(message);
  notification.warning({
    message: message,
    closeIcon: closeIcon,
    icon: <FontAwesomeIcon icon={faInfoCircle} />,
    duration: 5,
    top: topPosition,
    style: {
      color: colors.orange,
      background: backgroundColor
    }
  });
}

export function notifyError(message) {
  trackEvent(message);
  notification.error({
    message: message,
    closeIcon: closeIcon,
    icon: <FontAwesomeIcon icon={faGhost} />,
    duration: 5,
    top: topPosition,
    style: {
      color: colors.red,
      background: backgroundColor
    }
  });
}
