import './PriceChart.less';
import * as React from 'react';
import { widget } from '../../../charting_library/charting_library.min';
import colors from 'colors';

import DataFeed from './api/';

// Tutorial: https://medium.com/@jonchurch/tradingview-charting-library-js-api-setup-for-crypto-part-1-57e37f5b3d5a
export class PriceChart extends React.PureComponent {
  static defaultProps = {
    interval: '1',
    timezone: Intl.DateTimeFormat().resolvedOptions().timeZone,
    language: 'en',
    containerId: 'tv_chart_container',
    datafeedUrl: 'https://demo_feed.tradingview.com',
    libraryPath: '/charting_library/',
    chartsStorageUrl: 'https://saveload.tradingview.com',
    chartsStorageApiVersion: '1.1',
    clientId: 'tradingview.com',
    userId: 'public_user_id',
    fullscreen: false,
    autosize: true,
    studiesOverrides: {
      'moving average exponential.plot.color': '#696A9B',
      'moving average exponential.plot.linewidth': 2,
      'moving average exponential.precision': 6,
      'volume.volume ma.transparency': 100,
      'volume.show ma': false
    }
  };

  tvWidget = null;

  componentDidMount() {
    this.initTvWidget(this.props.tokens);
  }

  componentDidUpdate(prevProps, prevState) {
    if (
      prevProps.market !== this.props.market ||
      this.props.tokens !== prevProps.tokens
    ) {
      // https://github.com/tradingview/charting_library/wiki/Chart-Methods
      // this.tvWidget.setSymbol(this.props.market, this.props.interval, () => {
      //   console.log('Reload completed. this.tvWidget', this.tvWidget);
      // });
      this.initTvWidget(this.props.tokens);
    }
    if (
      prevProps.upColor !== this.props.upColor ||
      prevProps.downColor !== this.props.downColor
    ) {
      this.initTvWidget(this.props.tokens);
    }
  }

  componentWillUnmount() {
    if (this.tvWidget !== null) {
      this.tvWidget.remove();
      this.tvWidget = null;
    }
  }

  initTvWidget(tokens) {
    const widgetOptions = {
      debug: false,
      symbol: this.props.market,
      // BEWARE: no trailing slash is expected in feed URL
      datafeed: new DataFeed(tokens),
      // https://github.com/tradingview/charting_library/wiki/Resolution
      interval: this.props.interval,

      timezone: this.props.timezone,
      container_id: this.props.containerId,
      library_path: this.props.libraryPath,

      // https://github.com/tradingview/charting_library/wiki/Localization
      locale: this.props.language,

      // https://github.com/tradingview/charting_library/wiki/Featuresets
      disabled_features: [
        'display_market_status',
        // "create_volume_indicator_by_default", volume
        // 'volume_force_overlay', // k
        // 'create_volume_indicator_by_default_once',
        'header_symbol_search', // 
        'header_screenshot', // 
        'header_fullscreen_button', // 
        // "header_indicators",
        'header_settings',
        'header_resolutions', //
        'header_undo_redo', // 
        'header_compare', // compare
        'header_chart_type', // 
        'header_undo_redo', // 
        'header_symbol_search', // 
        'main_series_scale_menu',
        'property_pages',
        'left_toolbar', // 
        'header_widget',
        'header_widget_dom_node', // 
        'border_around_the_chart', // 
        'pane_context_menu', // 
        'timeframes_toolbar', // 
        'timezone_menu',
        'symbol_search_hot_key', // 
        'caption_buttons_text_if_possible',
        'control_bar',
        'context_menus', //
        'use_localstorage_for_settings',
        'scales_context_menu',
        'show_chart_property_page',
        'edit_buttons_in_legend',
        'move_logo_to_main_pane',
        'delete_button_in_legend',
        'show_hide_button_in_legend'
      ],

      enabled_features: [
        // Volume indicator shows 0.01 n/a #3797
        // https://github.com/tradingview/charting_library/issues/3797
        'hide_last_na_study_output'
        // 'move_logo_to_main_pane'
      ],
      charts_storage_url: this.props.chartsStorageUrl,
      charts_storage_api_version: this.props.chartsStorageApiVersion,
      client_id: this.props.clientId,
      user_id: this.props.userId,
      fullscreen: this.props.fullscreen,
      autosize: this.props.autosize,
      studies_overrides: this.props.studiesOverrides,
      theme: 'Dark',

      time_frames: [
        {
          text: '1hr',
          resolution: '60',
          description: '1 Hour',
          title: '1H'
        }
      ],
      // https://github.com/stevenGame/jr-chart/blob/f9cbe3497c781029473b05014ee9c4e61f506670/docs/Overrides.md
      overrides: {
        editorFontsList: ['Montserrat', 'sans-serif'],
        volumePaneSize: 'small',
        'symbolWatermarkProperties.color': 'rgba(0, 0, 0, 0)',
        'paneProperties.topMargin': 30, // 
        'paneProperties.bottomMargin': 20, // 
        'paneProperties.background': '#292B42', // Background
        'paneProperties.vertGridProperties.color': '#00000000',
        'paneProperties.horzGridProperties.color': '#00000000',
        'paneProperties.legendProperties.showLegend': true,
        'symbolWatermarkProperties.transparency': 100,
        'mainSeriesProperties.candleStyle.upColor': this.props.upColor,
        'mainSeriesProperties.candleStyle.downColor': this.props.downColor,
        'mainSeriesProperties.candleStyle.borderUpColor': this.props.upColor,
        'mainSeriesProperties.candleStyle.borderDownColor': this.props
          .downColor,
        'mainSeriesProperties.candleStyle.wickUpColor': this.props.upColor,
        'mainSeriesProperties.candleStyle.wickDownColor': this.props.downColor,
        'scalesProperties.lineColor': '#242539',
        'scalesProperties.textColor': '#696A9B',
        'mainSeriesProperties.priceLineWidth': 1,
        'mainSeriesProperties.showPriceLine': true,
        'scalesProperties.showSymbolLabels': false,
        'scalesProperties.fontSize': 11.2, // 0.85rem of 14px
        'scalesProperties.showSeriesLastValue': true,
        'paneProperties.legendProperties.showStudyArguments': true, // Hide ohlc button in trading view #424
        'paneProperties.legendProperties.showStudyTitles': true,
        'paneProperties.legendProperties.showStudyValues': true,
        'paneProperties.legendProperties.showSeriesTitle': false,
        'paneProperties.legendProperties.showSeriesOHLC': true, // enable OHLC
        'paneProperties.crossHairProperties.color': '#696A9B'
      }
    };

    this.tvWidget = new widget(widgetOptions);
    this.tvWidget.onChartReady(() => {
      // https://github.com/tradingview/charting_library/blob/e49b50a9bf8f57d2601dbba63b6e10a42c3c9f1d/charting_library/charting_library.min.d.ts#L859-L860
      // https://github.com/tradingview/charting_library/wiki/Studies-Overrides
      this.tvWidget
        .chart()
        .createStudy('Moving Average Exponential', false, false, [9]);
    });
  }

  render() {
    return (
      <div>
        <div
          style={{
            backgroundColor: colors.foreground
          }}
          id={this.props.containerId}
          className={'PriceChart'}
        />
      </div>
    );
  }
}
