import { request } from '../../../common';

export async function cancelOrders(
  accountId,
  orderHash,
  clientOrderId,
  signed,
  apiKey
) {
  const data = {
    accountId: accountId,
    orderHash: orderHash,
    clientOrderId: clientOrderId
  };
  const signature = signed.Rx + ',' + signed.Ry + ',' + signed.s;
  const headers = {
    'X-API-KEY': apiKey,
    'X-API-SIG': signature
  };
  return await request({
    method: 'POST',
    url: '/api/v1/deleteOrders',
    headers: headers,
    data
  });
}
