import { connect } from 'react-redux';
import { fetchDeposits, fetchWithdrawals } from 'redux/actions/MyAccountPage';
import React from 'react';

import { compareDexAccounts } from 'components/services/utils';
import DepositWithdrawalBaseTable from 'pages/account/components/DepositWithdrawalBaseTable';

import './BalanceTable.less'; // TODO: remove this dependency

class DepositWithdrawalTable extends React.Component {
  componentDidMount() {
    this.loadData();
  }

  componentDidUpdate(prevProps, prevState) {
    if (
      prevProps.dexAccount &&
      prevProps.dexAccount.account &&
      this.props.dexAccount &&
      this.props.dexAccount.account &&
      this.props.dexAccount.account.accountId &&
      compareDexAccounts(prevProps.dexAccount, this.props.dexAccount) ===
        false &&
      this.props.dexAccount.account.apiKey
    ) {
      this.loadData();
    }

    if (prevProps.tabs.type4 !== this.props.tabs.type4) {
      this.loadData();
    }
  }

  loadData(offset = -1) {
    try {
      const {
        dexAccount,
        tokens,
        balances,
        fetchDeposits,
        fetchWithdrawals
      } = this.props;
      if (
        !!dexAccount.account.accountId &&
        !!dexAccount.account.keyPairCipherText &&
        dexAccount.account.apiKey
      ) {
        if (this.props.type === 'deposit') {
          fetchDeposits(
            balances.depositLimit,
            offset !== -1 ? offset : balances.depositOffset,
            dexAccount.account.accountId,
            dexAccount.account.apiKey,
            tokens
          );
        } else {
          fetchWithdrawals(
            balances.withdrawalLimit,
            offset !== -1 ? offset : balances.withdrawalOffset,
            dexAccount.account.accountId,
            dexAccount.account.apiKey,
            tokens
          );
        }
      }
    } catch (err) {}
  }

  onChange = page => {
    const offset =
      this.props.type === 'deposit'
        ? this.props.balances.depositLimit * (page - 1)
        : this.props.balances.withdrawalLimit * (page - 1);
    this.loadData(offset);
  };

  render() {
    const { type, balances } = this.props;
    let data;
    let total;
    let limit;
    let current;

    if (type === 'deposit') {
      data = balances.deposits;
      total = balances.depositTotalNum;
      limit = balances.depositLimit;
      current = Math.floor(balances.depositOffset / limit) + 1;
    } else {
      data = balances.withdrawals;
      total = balances.withdrawalTotalNum;
      limit = balances.withdrawalLimit;
      current = Math.floor(balances.withdrawalOffset / limit) + 1;
    }

    return (
      <DepositWithdrawalBaseTable
        placeHolder={type === 'deposit' ? 'NoDeposits' : 'NoWithdrawals'}
        data={data}
        total={total}
        limit={limit}
        current={current}
        onChange={this.onChange}
      />
    );
  }
}

const mapStateToProps = state => {
  const { dexAccount, balances, tabs, exchange } = state;
  return { dexAccount, balances, tabs, tokens: exchange.tokens };
};

const mapDispatchToProps = dispatch => {
  return {
    fetchDeposits: (limit, offset, accountId, apiKey, tokens) =>
      dispatch(fetchDeposits(limit, offset, accountId, apiKey, tokens)),
    fetchWithdrawals: (limit, offset, accountId, apiKey, tokens) =>
      dispatch(fetchWithdrawals(limit, offset, accountId, apiKey, tokens))
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(DepositWithdrawalTable);
