export function sleep(ms) {
  return new Promise(resolve => setTimeout(resolve, ms));
}

export async function getApiKey(data, signed) {
  const signature = signed.Rx + ',' + signed.Ry + ',' + signed.s;
  const headers = {
    'X-API-SIG': signature
  };
  return await request({
    method: 'POST',
    url: '/api/v1/getApiKey',
    headers: headers,
    data
  });
}

