import { UPDATE_COLOR } from 'redux/actions/ColorManager';
import { getLanguage } from 'lightcone/api/localStorgeAPI';
import colors from 'colors';

// Internatioanl buy/sell up/down colors
const zhColorTheme = {
  up: colors.red,
  down: colors.green,

  buyPrimary: colors.green,
  buySecondary: colors.green + 'BB',
  buyBar: colors.green,

  sellPrimary: colors.red,
  sellSecondary: colors.red + 'BB',
  sellBar: colors.red
};

const colorTheme = {
  up: colors.green,
  down: colors.red,

  buyPrimary: colors.green,
  buySecondary: colors.green + 'BB',
  buyBar: colors.green,

  sellPrimary: colors.red,
  sellSecondary: colors.red + 'BB',
  sellBar: colors.red
};

const initialState = getLanguage() === 'zh' ? zhColorTheme : colorTheme;

export const ColorManagerReducer = (state = initialState, action) => {
  switch (action.type) {
    case UPDATE_COLOR: {
      const language = action.payload.language;
      if (language === 'zh') {
        return {
          ...state,
          ...zhColorTheme
        };
      } else {
        return {
          ...state,
          ...colorTheme
        };
      }
    }
    default:
      return state;
  }
};
