import { UPDATE_TOKENS } from 'redux/actions/Tokens';

const initialState = {
  tokens: []
};

export const TokensReducer = (state = initialState, action) => {
  switch (action.type) {
    case UPDATE_TOKENS:
      const tokens = action.payload.tokens;
      return {
        ...state,
        tokens
      };
    default:
      return state;
  }
};
