import { connect } from 'react-redux';
import I from 'components/I';
import React from 'react';

import colors from 'colors';

import {
  CancelOrderButton,
  LargeTableContainer,
  LargeTableRow,
  LargeTableRowFailed,
  LargeTableRowProcessed,
  LargeTableRowStatus,
  TextCompactTableHeader
} from 'styles/Styles';
import { ConfigProvider, Pagination, Table } from 'antd';
import { cancelOrders } from 'lightcone/api/v1/orders';
import { notifyError, notifySuccess } from 'redux/actions/Notification';
import EmptyTableIndicator from 'components/EmptyTableIndicator';
import Moment from 'moment';

import 'pages/account/components/BalanceTable.less';

class OrderBaseTable extends React.Component {
  onClickCancel = order => {
    // Send cancel request...
    (async () => {
      try {
        const apiKey = this.props.dexAccount.account.apiKey;
        // Use password to decode
        if (this.props.dexAccount.password === null) {
          throw new Error(
            'Password is not correct! Please try again or reset Password.'
          );
        }
        const signed = window.wallet.submitFlexCancel(
          order.hash,
          order.clientOrderId
        );

        await cancelOrders(
          this.props.dexAccount.account.accountId,
          signed.hash,
          signed.clientOrderId,
          signed.signature,
          apiKey
        );
        notifySuccess(<I s="Your order has been cancelled." />);
      } catch (err) {
        notifyError(<I s="Failed to cancel your order." />);
      }
    })();
  };

  render() {
    const customizeRenderEmpty = () => (
      <EmptyTableIndicator text={'NoHistoryOrders'} />
    );

    const columns = [
      {
        title: '',
        dataIndex: 'padding',
        width: 14
      },
      {
        title: (
          <TextCompactTableHeader>
            <I s="Side" />
          </TextCompactTableHeader>
        ),
        dataIndex: 'side'
      },
      {
        title: (
          <TextCompactTableHeader>
            <I s="Filled_Rate" />
          </TextCompactTableHeader>
        ),
        dataIndex: 'filled'
      },
      {
        title: (
          <TextCompactTableHeader>
            <I s="Market" />
          </TextCompactTableHeader>
        ),
        dataIndex: 'market'
      },
      {
        title: (
          <TextCompactTableHeader>
            <I s="Size" />
          </TextCompactTableHeader>
        ),
        dataIndex: 'size'
      },
      {
        title: (
          <TextCompactTableHeader>
            <I s="Price" />
          </TextCompactTableHeader>
        ),
        dataIndex: 'price'
      },
      {
        title: (
          <TextCompactTableHeader>
            <I s="Fee" />
          </TextCompactTableHeader>
        ),
        dataIndex: 'fee'
      },
      {
        title: (
          <TextCompactTableHeader>
            <I s="Timestamp" />
          </TextCompactTableHeader>
        ),
        dataIndex: 'date',
        width: 180
      },
      {
        title: (
          <TextCompactTableHeader
            style={{
              paddingRight: '14px'
            }}
          >
            <I s="Status/Operations" />
          </TextCompactTableHeader>
        ),
        dataIndex: 'status'
      }
    ];

    const data = [];
    for (let i = 0; i < this.props.data.length; i++) {
      const order = this.props.data[i];

      var status = '-';
      if (order.status === 'processing' || order.status === 'waiting') {
        status = (
          <CancelOrderButton
            onClick={e => {
              e.preventDefault();
              this.onClickCancel(order);
            }}
          >
            <I s="Cancel" />
          </CancelOrderButton>
        );
      } else if (order.status === 'processed') {
        status = (
          <LargeTableRowProcessed>
            <div>
              <I s="Filled" />
            </div>
          </LargeTableRowProcessed>
        );
      } else if (order.status === 'failed') {
        status = (
          <LargeTableRowFailed>
            <div>
              <I s="Failed" />
            </div>
          </LargeTableRowFailed>
        );
      } else if (
        order.status === 'cancelling' ||
        order.status === 'cancelled'
      ) {
        status = (
          <LargeTableRowStatus>
            <div>
              <I s="Cancelled" />
            </div>
          </LargeTableRowStatus>
        );
      } else if (order.status === 'expired') {
        status = (
          <LargeTableRowStatus>
            <div>
              <I s="Expired" />
            </div>
          </LargeTableRowStatus>
        );
      }
      data.push({
        key: i,
        side: (
          <LargeTableRow
            style={{
              color:
                order.side === 'BUY'
                  ? this.props.colorManager.buyPrimary
                  : this.props.colorManager.sellPrimary
            }}
          >
            {order.side === 'BUY' ? <I s="Buy" /> : <I s="Sell" />}
          </LargeTableRow>
        ),
        market: <LargeTableRow>{order.market}</LargeTableRow>,
        size: <LargeTableRow>{order.sizeInString} </LargeTableRow>,
        filled: (
          <LargeTableRow
            style={{
              color: colors.textWhite
            }}
          >
            {order.filled}
          </LargeTableRow>
        ),
        price: (
          <LargeTableRow
            style={{
              color:
                order.side === 'BUY'
                  ? this.props.colorManager.buyPrimary
                  : this.props.colorManager.sellPrimary
            }}
          >
            {order.price}
          </LargeTableRow>
        ),
        fee: (
          <LargeTableRow
            style={{
              color: colors.textDim
            }}
          >
            {order.feeInString}{' '}
            {order.feeInString !== '-'
              ? order.side === 'BUY'
                ? order.market.split('-')[0]
                : order.market.split('-')[1]
              : ''}
          </LargeTableRow>
        ),
        date: (
          <LargeTableRow
            style={{
              color: colors.textDim
            }}
          >
            {Moment(order.createdAt).format('YYYY-MM-DD HH:mm:ss')}
          </LargeTableRow>
        ),
        status: (
          <div
            style={{
              textAlign: 'right',
              paddingRight: '14px'
            }}
          >
            {status}
          </div>
        )
      });
    }

    const hasPagination = this.props.total > this.props.limit;

    return (
      <LargeTableContainer className={'my-table'}>
        <ConfigProvider renderEmpty={data.length === 0 && customizeRenderEmpty}>
          <Table
            style={{
              borderStyle: 'none',
              borderWidth: '0px',
              height: `${data.length * 34 + 35}px`,
              minHeight: '500px'
            }}
            size="middle"
            columns={columns}
            dataSource={data}
            pagination={false}
            scroll={{
              y: `${data.length * 34}px`
            }}
          />
          {hasPagination ? (
            <Pagination
              style={{
                padding: '30px 0px 30px 0px',
                background: colors.background,
                textAlign: 'center'
              }}
              size=""
              total={this.props.total}
              current={this.props.current}
              onChange={this.props.onChange}
              pageSize={this.props.limit}
            />
          ) : (
            <div />
          )}
        </ConfigProvider>
      </LargeTableContainer>
    );
  }
}

const mapStateToProps = state => {
  const { colorManager, dexAccount, myOrders, market } = state;
  return { colorManager, dexAccount, myOrders, market };
};

const mapDispatchToProps = dispatch => {
  return {};
};

export default connect(mapStateToProps, mapDispatchToProps)(OrderBaseTable);
