import { Button, Spin } from 'antd';
import { MyModal } from './styles/Styles';
import { connect } from 'react-redux';
import { logoutAll } from 'redux/actions/DexAccount';
import { showLogoutModal } from 'redux/actions/ModalManager';
import AppLayout from 'AppLayout';
import I from 'components/I';
import ModalIndicator from 'modals/components/ModalIndicator';
import React from 'react';
import Wallet from 'lightcone/wallet/wallet';
import colors from 'colors';
import styled from 'styled-components';

import {
  AddressDiv,
  Instruction,
  Section,
  TextPopupTitle
} from 'modals/styles/Styles';
import Group from 'modals/components/Group';

const MyButton = styled(Button)`
  font-size: 1rem !important;
  font-weight: 600 !important;
  height: 40px !important;
  width: 180px;
  border-style: none !important;
  border-radius: 20px !important;
  color: ${colors.textBright}!important;
  text-transform: uppercase !important;
  transition: 1s !important;
`;

const ConfirmButton = styled(MyButton)`
  margin-right: 20px;
  background: ${colors.red}!important;
`;

const CancelButton = styled(MyButton)`
  margin-left: 20px;
  background: ${colors.theme}!important;
`;

class LogoutModal extends React.Component {
  state = {
    loading: false
  };
  onClick = () => {
    this.setState({ loading: true });
    setTimeout(() => {
      window.wallet = new Wallet(window.wallet.web3, window.wallet.address);
      this.props.logoutAll();
      this.onClose();
      this.setState({ loading: false });
    }, 100);
  };

  onClose = () => {
    this.props.closeModal();
  };

  render() {
    return (
      <MyModal
        width={AppLayout.modalWidth}
        title={
          <TextPopupTitle>
            <I s="Logout" />
          </TextPopupTitle>
        }
        footer={null}
        closable={false}
        maskClosable={false}
        visible={this.props.isVislble}
        onCancel={() => this.onClose()}
      >
        <Spin
          spinning={this.state.loading}
          indicator={<ModalIndicator title="Logging out..." marginTop="80px" />}
        >
          <Section>
            <Instruction>
              <I s="LogoutInstruct_1" />
            </Instruction>
            <Instruction>
              <I s="LogoutInstruct_2" />
            </Instruction>
          </Section>
          <Section>
            <Group label={<I s="My Address" />}>
              <AddressDiv>{this.props.address}</AddressDiv>
            </Group>
          </Section>
          <Section
            style={{
              textAlign: 'center'
            }}
          >
            <ConfirmButton onClick={() => this.onClick()}>
              <I s="Yes, log me out." />
            </ConfirmButton>
            <CancelButton onClick={() => this.onClose()}>
              <I s="Cancel" />
            </CancelButton>
          </Section>
        </Spin>
      </MyModal>
    );
  }
}

const mapStateToProps = state => {
  const { modalManager, dexAccount } = state;
  const isVislble = modalManager.isLogoutModalVisible;
  const address = dexAccount.account.address;
  return { isVislble, address };
};

const mapDispatchToProps = dispatch => {
  return {
    closeModal: () => dispatch(showLogoutModal(false)),
    logoutAll: () => dispatch(logoutAll())
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(LogoutModal);
