import { getCandlestick } from 'lightcone/api/LightconeAPI';

import config from 'lightcone/config';

export const UPDATE_CANDLE = 'UPDATE_CANDLE';

export function updateCandles(ohlc) {
  return {
    type: UPDATE_CANDLE,
    payload: {
      ohlc
    }
  };
}

export function fetchCandles(market, configTokens) {
  return dispatch => {
    (async () => {
      try {
        var newCandles = [];
        const candles = await getCandlestick(market, '1min');
        for (var i = 0; i < candles.length; i = i + 1) {
          const candle = candles[i];
          const tokens = market.split('-');
          const baseToken = tokens[0];
          const quoteToken = tokens[1];

          let parsedCandle = {
            close: Number(candle['close']),
            high: Number(candle['high']),
            low: Number(candle['low']),
            open: Number(candle['open']),
            date: new Date(candle['start']),
            volume: config.fromWEI(quoteToken, candle['volume'], configTokens),
            size: config.fromWEI(baseToken, candle['size'], configTokens)
          };
          newCandles.push(parsedCandle);
        }

        // Requires at last two data points to draw candlestick
        if (newCandles && newCandles.length > 1) {
          newCandles = newCandles.sort((a, b) => (a.date > b.date ? 1 : -1));
          dispatch(updateCandles(newCandles));
        }
      } catch (error) {
        console.log(error);
      }
    })();
  };
}
