import { UPDATE_MARKET_LIST } from 'redux/actions/market/MarketList';

const initialState = {
  markets: [],
  marketNames: []
};

export const MarketListReducer = (state = initialState, action) => {
  switch (action.type) {
    case UPDATE_MARKET_LIST:
      const markets = action.payload.markets;
      const marketNames = markets.map(val => {
        return val.market;
      });
      return {
        ...state,
        markets,
        marketNames
      };
    default:
      return state;
  }
};
