import isEmpty from 'lodash/isEmpty';

export const FETCH_ACCOUNTS = 'FETCH_ACCOUNTS';
export const FETCH_ACCOUNTS_DATA_SUCCESS = 'FETCH_ACCOUNTS_DATA_SUCCESS';

export const FETCH_ACCOUNTS_FULFILLED = 'FETCH_ACCOUNTS_FULFILLED';
export const FETCH_ACCOUNTS_REJECTED = 'FETCH_ACCOUNTS_REJECTED';

export function itemsFetchDataSuccess(items) {
  return {
    type: FETCH_ACCOUNTS_DATA_SUCCESS,
    payload: {
      items
    }
  };
}

export function fetchAccounts() {
  return dispatch => {
    new Promise((resolve, reject) => {
      var { web3 } = window;
      const ethAccounts = getAccounts();
      if (isEmpty(ethAccounts)) {
        web3 &&
          web3.eth &&
          web3.eth.getAccounts((err, accounts) => {
            if (err) {
              reject(err);
            } else {
              dispatch(itemsFetchDataSuccess(accounts));
              resolve(accounts);
            }
          });
      } else {
        dispatch(itemsFetchDataSuccess(ethAccounts));
        resolve(ethAccounts);
      }
    });
  };
}

// Async with getAccounts
// https://ethereum.stackexchange.com/questions/54564/async-with-getaccounts
function promisify(fun, params = []) {
  return new Promise((resolve, reject) => {
    fun(...params, (err, data) => {
      if (err !== null) reject(err);
      else resolve(data);
    });
  });
}

export async function getAccounts() {
  try {
    const { web3 } = window;
    // throws if no account selected
    const promise = promisify(web3.eth.getAccounts);
    const accounts = await promise;
    return accounts;
  } catch (e) {
    return [];
  }
}
