import { request } from '../../../common';
import config from '../../../config';

export async function getDepth(market, level, limit, configTokens) {
  const data = {
    market,
    level,
    limit
  };
  const tokens = market.split('-');
  const baseToken = tokens[0];

  const response = await request({
    method: 'POST',
    url: '/api/v1/depth',
    data
  });

  const depth = response['depth'];
  const bids = depth['bids'];
  let updatedBids = [];
  for (let i = 0; i < bids.length; i = i + 1) {
    const bid = bids[i];
    let updatedBid = {
      price: bid.price,
      size: bid.size,
      volume: bid.volume,
      count: bid.count,
      sizeInNumber: config.fromWEI(baseToken, bid.size, configTokens)
    };
    updatedBids.push(updatedBid);
  }

  const asks = depth['asks'];
  let updatedAsks = [];
  for (let i = 0; i < asks.length; i = i + 1) {
    const ask = asks[i];
    let updatedAsk = {
      price: ask.price,
      size: ask.size,
      volume: ask.volume,
      count: ask.count,
      sizeInNumber: config.fromWEI(baseToken, ask.size, configTokens)
    };
    updatedAsks.push(updatedAsk);
  }

  return {
    bids: updatedBids,
    asks: updatedAsks,
    version: depth['version']
  };
}
