import {
  EMPTY_BALANCES,
  EMPTY_DEPOSITS,
  EMPTY_WITHDRAWALS,
  UPDATE_BALANCE,
  UPDATE_DEPOSITS,
  UPDATE_ETH_BALANCE,
  UPDATE_MY_BALANCES,
  UPDATE_TOKEN_BALANCE,
  UPDATE_WITHDRAWALS
} from 'redux/actions/MyAccountPage';
import config from 'lightcone/config';

const initialState = {
  balances: [],

  depositOffset: 0,
  depositLimit: 20,
  depositTotalNum: 0,
  deposits: [],

  withdrawalOffset: 0,
  withdrawalLimit: 20,
  withdrawalTotalNum: 0,
  withdrawals: []
};

export const MyAccountPageReducer = (state = initialState, action) => {
  switch (action.type) {
    case UPDATE_BALANCE: {
      const balance = action.payload.balance;
      const previousBalances = [...state.balances];
      const index = previousBalances.findIndex(
        ba => ba.tokenId === balance.tokenId
      );
      if (index !== -1) {
        previousBalances.splice(index, 1);
      }

      previousBalances.push(balance);
      return {
        ...state,
        balances: previousBalances
      };
    }

    case UPDATE_MY_BALANCES: {
      const previousBalances = [...state.balances];
      const newBalances = action.payload.balances;
      for (var i = 0; i < newBalances.length; i++) {
        var found = false;
        for (var j = 0; j < previousBalances.length; j++) {
          if (previousBalances[j].tokenId === newBalances[i].tokenId) {
            found = true;
            previousBalances[j].frozenAmount = newBalances[i].frozenAmount;
            previousBalances[j].totalAmount = newBalances[i].totalAmount;
            previousBalances[j].totalAmountInString =
              newBalances[i].totalAmountInString;
            previousBalances[j].frozenAmountInString =
              newBalances[i].frozenAmountInString;
            previousBalances[j].percentage = newBalances[i].percentage;
            previousBalances[j].available = newBalances[i].available;
            break;
          }
        }
        if (!found) {
          previousBalances.push(newBalances[i]);
        }
      }
      return {
        ...state,
        balances: previousBalances
      };
    }

    case UPDATE_ETH_BALANCE: {
      const ethWalletAmount = config.fromWEI(
        'ETH',
        action.payload.balance,
        action.payload.tokens
      );
      return {
        ...state,
        ethWalletAmount
      };
    }

    case UPDATE_TOKEN_BALANCE: {
      const symbol = action.payload.symbol;
      const lrcWalletAmount = config.fromWEI(
        symbol,
        action.payload.balance,
        action.payload.tokens
      );
      return {
        ...state,
        lrcWalletAmount
      };
    }

    case EMPTY_BALANCES: {
      return {
        ...state,
        balances: []
      };
    }

    case UPDATE_DEPOSITS: {
      return {
        ...state,
        depositOffset: action.payload.offset,
        depositLimit: action.payload.limit,
        depositTotalNum: action.payload.totalNum,
        deposits: action.payload.transactions
      };
    }

    case EMPTY_DEPOSITS: {
      return {
        ...state,
        depositOffset: 0,
        depositLimit: 20,
        depositTotalNum: 0,
        deposits: []
      };
    }

    case UPDATE_WITHDRAWALS: {
      return {
        ...state,
        withdrawalOffset: action.payload.offset,
        withdrawalLimit: action.payload.limit,
        withdrawalTotalNum: action.payload.totalNum,
        withdrawals: action.payload.transactions
      };
    }

    case EMPTY_WITHDRAWALS: {
      return {
        ...state,
        withdrawalOffset: 0,
        withdrawalLimit: 20,
        withdrawalTotalNum: 0,
        withdrawals: []
      };
    }

    default:
      return state;
  }
};
