import { history } from 'redux/configureStore';
import I from 'components/I';
import MakrdownPage from './components/MarkdownPage';
import React from 'react';
import colors from 'colors';
import styled from 'styled-components';

import {
  MenuFontAwesomeIcon,
  SideBarButton,
  SideBarGroupLabel
} from 'components/SideBarDrawer';
import { faFileAlt } from '@fortawesome/free-solid-svg-icons/faFileAlt';
import { faTrophy } from '@fortawesome/free-solid-svg-icons/faTrophy';

const SideBarButtonAlt = styled(SideBarButton)`
  &[disabled],
  &[disabled]:hover {
    background-color: ${colors.foreground}!important;
    color: ${colors.textBright}!important;
  }
`;
const SideNavigation = ({ current }) => (
  <div style={{ padding: '24px 0' }}>
    <SideBarGroupLabel>
      <I s="Documentation" />
    </SideBarGroupLabel>
    <SideBarButtonAlt
      disabled={current === 'fees'}
      onClick={() => history.push('/document/fees')}
    >
      <MenuFontAwesomeIcon icon={faFileAlt} />
      <I s="Fee Schedule" />
    </SideBarButtonAlt>
    <SideBarButtonAlt
      disabled={current === 'listing'}
      onClick={() => history.push('/document/listing')}
    >
      <MenuFontAwesomeIcon icon={faFileAlt} />
      <I s="Token Listing" />
    </SideBarButtonAlt>
    <SideBarButtonAlt
      disabled={current === 'api'}
      onClick={() => history.push('/document/api')}
    >
      <MenuFontAwesomeIcon icon={faFileAlt} />
      <I s="Relayer API" />
    </SideBarButtonAlt>

    <SideBarGroupLabel>
      <I s="Beta Programs" />
    </SideBarGroupLabel>

    <SideBarButtonAlt
      hoverbg={colors.orange}
      colortxt={colors.orange}
      disabled={current === 'beta1'}
      onClick={() => history.push('/document/beta1')}
    >
      <MenuFontAwesomeIcon icon={faTrophy} />
      <I s="Beta1 Testing Rewards" />
    </SideBarButtonAlt>
  </div>
);

const FeesPage = () => {
  return (
    <MakrdownPage
      navigation={<SideNavigation current="fees" />}
      fileName="Fees.md"
    />
  );
};

const ListingPage = () => {
  return (
    <MakrdownPage
      navigation={<SideNavigation current="listing" />}
      fileName="Listing.md"
    />
  );
};

const APIPage = () => {
  return (
    <MakrdownPage
      navigation={<SideNavigation current="api" />}
      fileName="API.md"
    />
  );
};

const Beta1RewardsPage = () => {
  return (
    <MakrdownPage
      navigation={<SideNavigation current="beta1" />}
      fileName="Beta1Rewards.md"
    />
  );
};

export { FeesPage, APIPage, ListingPage, Beta1RewardsPage };
