import { connect } from 'react-redux';
import I from 'components/I';
import React from 'react';
import styled from 'styled-components';

import AppLayout from 'AppLayout';
import colors from 'colors';

import {
  CancelOrderButton,
  LargeTableRow,
  TextCompactTableHeader
} from 'styles/Styles';
import { ConfigProvider, Pagination, Table } from 'antd';
import { cancelOrders } from 'lightcone/api/v1/orders';
import EmptyTableIndicator from 'components/EmptyTableIndicator';
import Moment from 'moment';

import { notifyError, notifySuccess } from 'redux/actions/Notification';

import './OrderBaseTable.less';

const SmallPagination = styled(Pagination)`
  padding: 4px !important;
  text-align: right;
  a {
    color: colors.textDim + '!important';
  }
`;

class OrderBaseTable extends React.Component {
  onClickCancel = order => {
    // Send cancel request...
    (async () => {
      try {
        const apiKey = this.props.dexAccount.account.apiKey;
        // Use password to decode
        if (this.props.dexAccount.password === null) {
          throw new Error(
            'Password is not correct! Please try again or reset Password.'
          );
        }
        const signed = window.wallet.submitFlexCancel(
          order.hash,
          order.clientOrderId
        );
        await cancelOrders(
          this.props.dexAccount.account.accountId,
          signed.hash,
          signed.clientOrderId,
          signed.signature,
          apiKey
        );
        notifySuccess(<I s="Your order has been cancelled." />);
      } catch (err) {
        notifyError(<I s="Failed to cancel your order." />);
      }
    })();
  };

  render() {
    const emptyTableIndicator = () => (
      <EmptyTableIndicator text={'NoOpenOrders'} marginTop={'4%'} />
    );

    const columns = [
      {
        title: '',
        dataIndex: 'padding',
        width: 13
      },
      {
        title: (
          <TextCompactTableHeader>
            <I s="Side" />
          </TextCompactTableHeader>
        ),
        dataIndex: 'side',
        width: 50
      },
      {
        title: (
          <TextCompactTableHeader>
            <I s="Market" />
          </TextCompactTableHeader>
        ),
        dataIndex: 'market',
        width: 76
      },
      {
        title: (
          <TextCompactTableHeader>
            <I s="Filled_Rate" />
          </TextCompactTableHeader>
        ),
        dataIndex: 'filled',
        width: 70
      },
      {
        title: (
          <TextCompactTableHeader>
            <I s="Size" />
          </TextCompactTableHeader>
        ),
        dataIndex: 'size'
      },
      {
        title: (
          <TextCompactTableHeader>
            <I s="Price" />
          </TextCompactTableHeader>
        ),
        dataIndex: 'price'
      },
      {
        title: (
          <TextCompactTableHeader>
            <I s="Timestamp" />
          </TextCompactTableHeader>
        ),
        dataIndex: 'date',
        width: 100
      },
      {
        title: (
          <TextCompactTableHeader
            style={{
              paddingRight: '9px'
            }}
          >
            <I s="Status" />
          </TextCompactTableHeader>
        ),
        dataIndex: 'status'
      }
    ];

    const data = [];
    for (let i = 0; i < this.props.data.length; i++) {
      const order = this.props.data[i];

      var status = '-';
      if (order.status === 'processing' || order.status === 'waiting') {
        status = (
          <CancelOrderButton
            onClick={e => {
              e.preventDefault();
              this.onClickCancel(order);
            }}
          >
            <I s="Cancel" />
          </CancelOrderButton>
        );
      } else if (order.status === 'processed') {
        status = <I s="Filled" />;
      } else if (order.status === 'failed') {
        status = <I s="Failed" />;
      } else if (
        order.status === 'cancelling' ||
        order.status === 'cancelled'
      ) {
        status = <I s="Cancelled" />;
      } else if (order.status === 'expired') {
        status = <I s="Expired" />;
      }
      data.push({
        key: i,
        side: (
          <LargeTableRow
            style={{
              color:
                order.side === 'BUY'
                  ? this.props.colorManager.buyPrimary
                  : this.props.colorManager.sellPrimary
            }}
          >
            {order.side === 'BUY' ? <I s="Buy" /> : <I s="Sell" />}
          </LargeTableRow>
        ),
        market: <LargeTableRow>{order.market}</LargeTableRow>,
        size: <LargeTableRow>{order.sizeInString}</LargeTableRow>,
        filled: (
          <LargeTableRow
            style={{
              color: colors.textWhite
            }}
          >
            {order.filled}
          </LargeTableRow>
        ),
        price: (
          <LargeTableRow
            style={{
              color:
                order.side === 'BUY'
                  ? this.props.colorManager.buyPrimary
                  : this.props.colorManager.sellPrimary
            }}
          >
            {order.price}
          </LargeTableRow>
        ),
        date: (
          <LargeTableRow
            style={{
              color: colors.textDim
            }}
          >
            {Moment(order.createdAt).format('MM/DD HH:mm:ss')}
          </LargeTableRow>
        ),
        status: (
          <LargeTableRow
            style={{
              color: colors.textDim,
              textAlign: 'right',
              paddingRight: '9px'
            }}
          >
            {status}
          </LargeTableRow>
        )
      });
    }

    return (
      <div className={'open-orders'}>
        <ConfigProvider renderEmpty={data.length === 0 && emptyTableIndicator}>
          <Table
            style={{
              borderStyle: 'none',
              borderWidth: '0px',
              height:
                this.props.total > this.props.limit
                  ? AppLayout.tradeOrderBaseTableHeight
                  : AppLayout.tradeOrderBaseTableHeightNoPagination
            }}
            size="middle"
            tableLayout={'fixed'}
            columns={columns}
            dataSource={data}
            pagination={false}
            scroll={{
              y:
                this.props.total > this.props.limit
                  ? AppLayout.tradeOrderBaseTableScrollY
                  : AppLayout.tradeOrderBaseTableScrollYNoPagination
            }}
          />
          {this.props.total > this.props.limit ? (
            <SmallPagination
              size="small"
              style={{ background: colors.lightForeground }}
              total={this.props.total}
              current={this.props.current}
              onChange={this.props.onChange}
              pageSize={this.props.limit}
            />
          ) : (
            <div />
          )}
        </ConfigProvider>
      </div>
    );
  }
}

const mapStateToProps = state => {
  const { colorManager, dexAccount, myOrders, market } = state;
  return { colorManager, dexAccount, myOrders, market };
};

const mapDispatchToProps = dispatch => {
  return {};
};

export default connect(mapStateToProps, mapDispatchToProps)(OrderBaseTable);
