import { ActionButton } from 'styles/Styles';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { MyModal } from './styles/Styles';
import { faTimes } from '@fortawesome/free-solid-svg-icons/faTimes';
import AppLayout from 'AppLayout';
import I from 'components/I';
import PasswordRule from './components/PasswordRule';
import React from 'react';
import colors from 'colors';

import {
  AddressDiv,
  Section,
  TextFormError,
  TextPopupTitle
} from 'modals/styles/Styles';
import { Input, Spin } from 'antd';
import Group from 'modals/components/Group';
import ModalIndicator from 'modals/components/ModalIndicator';

class ConfirmPasswordModal extends React.Component {
  title = 'OVERRIDE';
  buttonLabel = 'OVERRIDE';
  instructions = '';
  onClose = () => {
    this.props.closeModal();
  };

  onProceed = () => {
    alert('override me');
  };

  state = {
    loading: false,
    isPasswordValid: true,
    password: '',
    repeatedPassword: '',
    passwordMismatch: false
  };

  componentDidUpdate(prevProps, prevState) {
    if (
      this.props.isVisible === false &&
      this.props.isVisible !== prevProps.isVisible
    ) {
      this.setState({
        loading: false,
        isPasswordValid: true,
        password: '',
        repeatedPassword: '',
        passwordMismatch: false
      });
    }
  }

  passwordValidationAggregator = ruleResult => {
    if (this.state.isPasswordValid && !ruleResult) {
      this.setState({ isPasswordValid: false });
    }
  };

  getIfPasswordsMismatch = (password1, password2) => {
    let p1 = password1 || '';
    let p2 = password2 || '';
    //return p2.length > p1.length || p1.substring(0, p2.length) !== p2;
    return p1 !== p2;
  };

  onPasswordChange = e => {
    const { value } = e.target;
    /**
     * repeated Passwordmismatch
     */
    const mismatch = !this.state.repeatedPassword
      ? false
      : this.getIfPasswordsMismatch(value, this.state.repeatedPassword);
    this.setState({
      isPasswordValid: true,
      password: value,
      passwordMismatch: mismatch
    });
  };

  onRepeatedPasswordChange = e => {
    const { value } = e.target;
    const mismatch = this.getIfPasswordsMismatch(this.state.password, value);
    this.setState({
      repeatedPassword: value,
      passwordMismatch: mismatch
    });
  };

  onPasswordEnter = e => {
    if (e.keyCode === 13) {
      e.preventDefault();
      if (
        this.state.password.length > 0 &&
        this.state.password === this.state.repeatedPassword &&
        this.state.isPasswordValid
      )
        this.onProceed();
    }
  };

  render() {
    return (
      <MyModal
        width={AppLayout.modalWidth}
        title={<TextPopupTitle>{this.title}</TextPopupTitle>}
        footer={null}
        maskClosable={false}
        closeIcon={<FontAwesomeIcon icon={faTimes} />}
        visible={this.props.isVisible}
        onCancel={() => this.onClose()}
      >
        <Spin
          indicator={
            <ModalIndicator
              title="metamaskConfirm"
              tips={[
                <div key="1">
                  <I s="metaMaskPendingTxTip" />
                </div>
              ]}
              imageUrl="/assets/images/metamask_pending.png"
              marginTop="80px"
            />
          }
          spinning={this.state.loading}
        >
          {this.instructions}
          <Section>
            <Group label={<I s="My Address" />}>
              <AddressDiv>{window.wallet && window.wallet.address}</AddressDiv>
            </Group>
            <Group label={<I s="Password" />}>
              <Input.Password
                autoFocus
                autoCapitalize={'off'}
                autoComplete={'off'}
                value={this.state.password}
                onChange={this.onPasswordChange}
                onBlur={this.onBlur}
                placeholder=""
                maxLength={128}
                style={{
                  color: colors.textWhite
                }}
              />
            </Group>
            <Group label={<I s="Confirm Password" />}>
              <Input.Password
                autoCapitalize={'off'}
                autoComplete={'off'}
                value={this.state.repeatedPassword}
                onChange={this.onRepeatedPasswordChange}
                onBlur={this.onBlur}
                placeholder=""
                maxLength={128}
                style={{
                  color: colors.textWhite
                }}
                onKeyDown={this.onPasswordEnter.bind(this)}
              />
              <div
                style={{
                  marginTop: '12px',
                  height: '20px'
                }}
              >
                <TextFormError>
                  {this.state.passwordMismatch ? (
                    <I s="PasswordMismatch" />
                  ) : (
                    ''
                  )}
                </TextFormError>
              </div>
            </Group>
            <Group>
              <div>
                <p>
                  <I s="PasswordRequirements" />
                </p>

                <PasswordRule
                  ruleText={<I s="PasswordRequirements_1" />}
                  password={this.state.password}
                  validator={password => {
                    return !!password.match(/[a-z]/g);
                  }}
                  aggregator={this.passwordValidationAggregator}
                />
                <PasswordRule
                  ruleText={<I s="PasswordRequirements_2" />}
                  password={this.state.password}
                  validator={password => {
                    return !!password.match(/[A-Z]/g);
                  }}
                  aggregator={this.passwordValidationAggregator}
                />
                <PasswordRule
                  ruleText={<I s="PasswordRequirements_3" />}
                  password={this.state.password}
                  validator={password => {
                    return !!password.match(/[0-9]/g);
                  }}
                  aggregator={this.passwordValidationAggregator}
                />
                <PasswordRule
                  ruleText={<I s="PasswordRequirements_4" />}
                  password={this.state.password}
                  ruleOK={this.state.hasSpecialLetter}
                  validator={password => {
                    return !!password.match(
                      /[\\/~`!@#$%^&*(){}[\]"':;?+=\-|_/>.<,"]/g
                    );
                  }}
                  aggregator={this.passwordValidationAggregator}
                />
                <PasswordRule
                  ruleText={<I s="PasswordRequirements_5" />}
                  password={this.state.password}
                  validator={password => {
                    return password.length >= 12 && password.length <= 128;
                  }}
                  aggregator={this.passwordValidationAggregator}
                />
              </div>
            </Group>
          </Section>
          {this.props.showLoginModal ? (
            <Section>
              <Group>
                <a
                  href
                  onClick={() => {
                    this.props.closeModal();
                    this.props.showLoginModal();
                  }}
                  style={{
                    display: 'inline-block',
                    float: 'right'
                  }}
                >
                  <I s="Try to login again?" />
                </a>
              </Group>
            </Section>
          ) : (
            <span />
          )}

          <Section>
            <ActionButton
              disabled={
                this.state.password.length === 0 ||
                this.state.password !== this.state.repeatedPassword ||
                !this.state.isPasswordValid
              }
              onClick={() => this.onProceed()}
            >
              {this.buttonLabel}
            </ActionButton>
          </Section>
        </Spin>
      </MyModal>
    );
  }
}

export default ConfirmPasswordModal;
