import { connect } from 'react-redux';
import I from 'components/I';
import React from 'react';

import { Instruction, Section } from 'modals/styles/Styles';
import {
  REGISTERING,
  updateAccount,
  updatePassword,
  updateRememberMe
} from 'redux/actions/DexAccount';
import { notifyError, notifySuccess } from 'redux/actions/Notification';
import { registerAccountModal } from 'redux/actions/ModalManager';
import WhyIcon from 'components/WhyIcon';

import { encrypt } from 'modals/components/utils';
import { formatter, utils } from 'lightcone/common';
import ConfirmPasswordModal from './ConfirmPasswordModal';
import config from 'lightcone/config';

class RegisterAccountModal extends ConfirmPasswordModal {
  title = (<I s="Register Account" />);
  buttonLabel = (<I s="Register Account" />);
  instructions = (
    <Section>
      <Instruction>
        <I s="RegisterAccountInstruction_1" />
      </Instruction>
      <ul>
        <li>
          <I s="RegisterAccountInstruction_Timing" />
          <WhyIcon text="TimingWhy" />
        </li>
        <li>
          <I s="RegisterAccountInstruction_Fee" />
          <WhyIcon text="RegisterAccountInstruction_FeeWhy" />
        </li>
      </ul>
    </Section>
  );

  onProceed = () => {
    this.setState({
      loading: true
    });
    if (this.state.password.length > 0 && this.state.isPasswordValid) {
      const { updateRememberMe, exchange, nonce, gasPrice } = this.props;
      const { password } = this.state;
      updateRememberMe(false);

      (async () => {
        try {
          const passwordHash = utils.keccakHash(password);
          const fee = formatter
            .toBig(config.getFeeByType('create', exchange.onchainFees).fee)
            .plus(config.getFeeByType('deposit', exchange.onchainFees).fee);
          const createOrUpdateAccountResponse = await window.wallet.createOrUpdateAccount(
            password,
            {
              exchangeAddress: exchange.exchangeAddress,
              fee: fee.toString(),
              chainId: exchange.chainId,
              token: config.getTokenBySymbol('ETH', exchange.tokens),
              amount: '',
              permission: '',
              nonce: nonce.nonce,
              gasPrice: gasPrice.gasPrice
            },
            true
          );

          const keyPair = createOrUpdateAccountResponse['keyPair'];

          if (keyPair.secretKey === undefined) {
            throw new Error(
              'Failed to parse the response of account creation.'
            );
          }
          const keyPairCipherText = encrypt(keyPair.secretKey, passwordHash);

          const account = {
            address: window.wallet.address,
            publicKeyX: keyPair.publicKeyX,
            publicKeyY: keyPair.publicKeyY,
            keyPairCipherText,
            state: REGISTERING,
            password: passwordHash
          };

          this.props.updateAccount(account);
          notifySuccess(<I s="AccountBeingRegisteredNotification" />);
        } catch (err) {
          notifyError(<I s="AccountRegistrationFailedNotofication" />);
        } finally {
          // Reset password
          this.setState({
            password: '',
            repeatedPassword: ''
          });

          this.onClose();
        }
      })();
    }
  };
}

const mapStateToProps = state => {
  const { modalManager, nonce, gasPrice, exchange } = state;
  const isVisible = modalManager.isRegisterAccountModalVisible;
  return { isVisible, modalManager, nonce, gasPrice, exchange };
};

const mapDispatchToProps = dispatch => {
  return {
    closeModal: () => dispatch(registerAccountModal(false)),
    updateAccount: account => dispatch(updateAccount(account)),
    updatePassword: password => dispatch(updatePassword(password)),
    updateRememberMe: rememberMe => dispatch(updateRememberMe(rememberMe))
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(RegisterAccountModal);
