import { getCandlestick } from 'lightcone/api/LightconeAPI';
import lightcone_v3_config from 'lightcone/config';

const supportedResolutions = ['1', '15', '240', 'D', '6D'];

const config = {
  supported_resolutions: supportedResolutions
};

export default class DataFeed {
  constructor(tokens) {
    this.tokens = tokens;
  }

  onReady(cb) {
    setTimeout(() => cb(config), 0);
  }

  searchSymbols(userInput, exchange, symbolType, onResultReadyCallback) {}

  resolveSymbol(symbolName, onSymbolResolvedCallback, onResolveErrorCallback) {
    // expects a symbolInfo object in response
    var symbol_stub = {
      name: symbolName,
      description: '',
      type: 'crypto',
      session: '24x7',
      timezone: 'Etc/UTC',
      exchange: '',
      minmov: 1,
      pricescale: 1000000,
      has_intraday: true,
      intraday_multipliers: ['1'],
      supported_resolution: supportedResolutions,
      volume_precision: 2,
      data_status: 'streaming'
    };

    /*
    var split_data = symbolName.split(/[:/]/);
		if (split_data[2].match(/USD|EUR|JPY|AUD|GBP|KRW|CNY/)) {
			symbol_stub.pricescale = 100
    }
    */

    setTimeout(function() {
      onSymbolResolvedCallback(symbol_stub);
    }, 0);

    // onResolveErrorCallback('Not feeling it today')
  }

  getBars(
    symbolInfo,
    resolution,
    from,
    to,
    onHistoryCallback,
    onErrorCallback,
    firstDataRequest
  ) {
    let market = symbolInfo['name'];

    (async () => {
      try {
        // Only 1hr returns data
        var candles = await getCandlestick(market, '1hr');
        candles.reverse();

        let startTimestamp;
        let endTimestamp;
        let quoteToken;
        let i;

        var newCandles = {};
        for (i = 0; i < candles.length; i = i + 1) {
          const candle = candles[i];
          const tokens = market.split('-');
          quoteToken = tokens[1];

          if (startTimestamp === undefined) {
            startTimestamp = candle['start'];
          } else {
            endTimestamp = candle['start'];
          }

          const start = candle['start'];

          let parsedCandle = {
            time: start,
            low: Number(candle['low']),
            high: Number(candle['high']),
            open: Number(candle['open']),
            close: Number(candle['close']),
            volume: Number(
              lightcone_v3_config.fromWEI(
                quoteToken,
                candle['volume'],
                this.tokens
              )
            )
          };
          newCandles[start] = parsedCandle;
        }

        var newMoreCandles = [];
        var lastCandle = newCandles[startTimestamp];
        for (i = startTimestamp; i <= endTimestamp; i = i + 3600000) {
          if (newCandles[i] === undefined) {
            lastCandle['time'] = i;
            newMoreCandles.push(Object.assign({}, lastCandle));
          } else {
            const candle = newCandles[i];
            newMoreCandles.push(candle);
            lastCandle = {
              time: candle['start'],
              low: Number(candle['close']),
              high: Number(candle['close']),
              open: Number(candle['close']),
              close: Number(candle['close']),
              volume: 0
            };
          }
        }

        if (firstDataRequest) {
          if (newMoreCandles) {
            onHistoryCallback(newMoreCandles, {
              noData: false
            });
          } else {
            onHistoryCallback(newMoreCandles, {
              noData: true
            });
          }
        }
      } catch (error) {}
    })();
  }
  subscribeBars(
    symbolInfo,
    resolution,
    onRealtimeCallback,
    subscribeUID,
    onResetCacheNeededCallback
  ) {
    // TradingView JS API()2-
    // https://www.jianshu.com/p/49cdc530262d
  }
  unsubscribeBars(subscriberUID) {}
  calculateHistoryDepth(resolution, resolutionBack, intervalBack) {
    // https://github.com/tradingview/charting_library/wiki/JS-Api#calculatehistorydepthresolution-resolutionback-intervalback
    // while optional, this makes sure we request 24 hours of minute data at a time
    // CryptoCompare's minute data endpoint will throw an error if we request data beyond 7 days in the past, and return no data
    return resolution < 60
      ? { resolutionBack: 'D', intervalBack: '14' }
      : undefined;
  }
  getMarks(symbolInfo, startDate, endDate, onDataCallback, resolution) {}
  getTimeScaleMarks(
    symbolInfo,
    startDate,
    endDate,
    onDataCallback,
    resolution
  ) {}
  getServerTime(cb) {}
}
