import { connect } from 'react-redux';
import I from 'components/I';
import React from 'react';

import colors from 'colors';

import { Button } from 'antd';
import { updateMyOrdersAndMyTradesType } from 'redux/actions/Tabs';

import './TabHeader.less';

class TabHeader extends React.Component {
  clickedOpenOrdersButton = () => {
    this.props.updateMyOrdersAndMyTradesType('open-orders');
    // this.props.increaseOpenOrdersReferenceCount();
  };

  clickedOrderHistory = () => {
    this.props.updateMyOrdersAndMyTradesType('history-orders');
    // this.props.increaseHistoryOrdersReferenceCount();
  };

  render() {
    const isBuy = this.props.tabs.type2 === 'open-orders' ? true : false;
    const isSell = this.props.tabs.type2 === 'history-orders' ? true : false;

    const buttonStyle = {
      color: colors.textWhite,
      backgroundColor: colors.background,
      borderStyle: 'none',
      height: '30px',
      borderRadius: '0px',
      fontWeight: '600',
      fontSize: '0.9rem',
      padding: '0px 2px',
      margin: '0px 15px 0px 0px',
      borderBottomWidth: '2px',
      borderBottomStyle: 'solid',
      borderBottomColor: 'transparent'
    };

    const buyButtonActiveStyle = {
      ...buttonStyle,
      color: colors.textWhite,
      borderBottomColor: colors.theme
    };

    const buttonInactiveStyle = {
      ...buttonStyle,
      color: colors.textDim
    };

    return (
      <div className="my-orders-header-tab-row">
        <Button
          style={isBuy ? buyButtonActiveStyle : buttonInactiveStyle}
          onClick={this.clickedOpenOrdersButton}
        >
          <I s="Open Orders" />
        </Button>
        <Button
          style={isSell ? buyButtonActiveStyle : buttonInactiveStyle}
          onClick={this.clickedOrderHistory}
        >
          <I s="Order History" />
        </Button>
      </div>
    );
  }
}

const mapStateToProps = state => {
  const { myOrders, tabs } = state;
  return {
    myOrders,
    tabs
  };
};

const mapDispatchToProps = dispatch => {
  return {
    updateMyOrdersAndMyTradesType: type =>
      dispatch(updateMyOrdersAndMyTradesType(type))
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(TabHeader);
